import React, { Component } from 'react'
import {TextField,InputLabel,Select,FormControl,withStyles } from '@material-ui/core';
import {convertCurrency} from '../commons/commonLib'
import axios from 'axios'
import { Redirect } from 'react-router-dom'
import {API,PLN_TOKEN_PRODUCT,APLUS,X_API_KEY,ORDER_PRODUCT} from '../../../api/index'
import Swal from 'sweetalert2'
import LoadingPage from '../../loadingPage'
import { connect } from 'react-redux';

class Tokenlisrik extends Component {
    constructor(){
        super()
        this.state={
            product:'',
            nomor:'',
            tipe:'Kosong',
            nominal:'',
            // harga:'',
            ep : '',
            name:'',
            code :'',
            nomorPelanggan:'',
            loading:true,
            redirect:'',
            tipeProduct:'Token Listik'
        }
        this.handleChangeProduct= this.handleChangeProduct.bind(this);
        this.handleChange= this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e){
        this.setState({loading:true})
        e.preventDefault();
        const {nomor, nomorPelanggan, code} = this.state
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        const header={
            'X-Api-Key' : X_API_KEY
        }
        const body = {
            'userID' :isLogin.userID,
            'accessToken' : isLogin.mbr_token,
            'aplUse': APLUS,
            'MSISDN':nomorPelanggan,
            'buyerPhone':nomor,
            'productCode':code
        }
        // console.log(body)
        axios.post(API+ORDER_PRODUCT, body, {headers:header} )
            .then(res => {
                const order = res.data;
                this.setState({ order });
                if(order.errNumber === '19' || order.errNumber === '23'){
                    Swal.fire({
                        icon : 'error',
                        title: order.status,
                        text : order.respMessage
                    })
                    this.setState({loading: false})
                }else{
                    this.setState({loading: false})
                    Swal.fire({
                        icon : 'success',
                        title: order.status,
                        text : order.respMessage,
                        allowOutsideClick: false,
                    }).then((result) => {
                        if (result.value) {
                            const keterangan ={
                                'product' : this.state.tipeProduct,
                                'tipe' : this.state.tipe,
                                'ep': this.state.ep,
                                'logo':this.state.logo
                            }
                            const data = {order,keterangan}
                            this.props.changeReport(data)
                            this.setState({ redirect:true })
                        }
                    })
                }
            })
            .catch(error => {
                console.log(error.response)
            });
    }
    handleChange(e){
        this.setState({[e.target.name]:e.target.value})
    }
    handleChangeProduct(e){
        this.setState({[e.target.name]:e.target.value})
        const nm = this.state.product.filter(
            (p) => {
                return p.code.indexOf(e.target.value) !== -1;
            }
        )
        // console.log(nm)
        this.setState({harga: nm[0].price})
        this.setState({ep: nm[0].ep})
        this.setState({name: nm[0].name})
        this.setState({code: nm[0].code})
    }
    componentDidMount(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        if (isLogin){
            const header={
                'X-Api-Key' : X_API_KEY
            }
            const body ={
                'userID' : isLogin.userID,
                'aplUse':APLUS,
                'accessToken' : isLogin.mbr_token
            }
            axios.post(API+PLN_TOKEN_PRODUCT, body, {headers:header} )
            .then(res => {
                const product = res.data.data;
                if(product.errNumber === '8'){
                    this.setState({loading:false})
                }else{
                    this.setState({ product: product, nomor:this.props.profile.o_hp });
                    this.setState({loading:false})
                }
                })
            .catch(error => {
            });
        }else{
            window.location = '/'
        }
        
    }

    render() {
        if(this.state.redirect === true){
            return <Redirect to='/home/detiletransaksi' />
        }
        if(this.state.loading === true){
            return(
                <LoadingPage/>
            )
        }
        return (
            <div>
                <div className=' border shadow p-3 mt-3 mb-3'>
                    <div className="row mt-2">
                        <div className='col-sm-12'>
                            <h3 style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} >Pembelian Token Listrik</h3>
                        </div>
                    </div>
                    <div className='row'>
                        <div className='col-sm mt-2' >
                            <GreenTextField label="Nomor Pelanggan" style={{width:'100%'}} 
                                name="nomorPelanggan" 
                                value={this.state.nomorPelanggan} 
                                onChange={this.handleChange}
                                autoComplete='off' 
                            />
                        </div>
                        <div className='col-sm mt-2' >
                            <GreenTextField label="Nomor Telepon" style={{width:'100%'}} 
                                name="nomor" 
                                value={this.state.nomor} 
                                onChange={this.handleChange}
                                autoComplete='off' 
                            />
                            <span style={{fontSize:'9px',color:'#D41C24'}}>No. Digunakan untuk menerima konfirmasi</span>
                        </div>
                        <div className='col-sm mt-2' >
                            <GreenFormControl style={{width:'100%'}}>
                                <InputLabel htmlFor="nominal-native-simple">Nominal</InputLabel>
                                { this.state.product === '' ? 
                                    <Select  native >
                                        <option value="" ></option>
                                    </Select> 
                                :
                                <Select
                                    native
                                    value={this.state.nominal}
                                    onChange={this.handleChangeProduct}
                                    name="nominal"
                                >
                                    <option value="" ></option>
                                    {this.state.product.map(product=>(
                                        <option className='mt-2' value={product.code} key={product.code}>{product.name}</option>
                                    ))}
                                </Select>
                                }
                            </GreenFormControl>
                        </div>
                        <div className='col-sm mt-2' >
                            <GreenTextField label="Point" style={{width:'100%'}} 
                                disabled value={this.state.ep}
                            />
                        </div>
                    </div>
                    <div className='row  mt-3'>
                        <div className='col-sm-9'>
                            <p style={{fontSize:'12px', color:'#707070',width:'100px'}} className='mb-0'>Harga</p>
                            <span style={{fontSize:'36px', color:'#2E7D32',width:'200px'}} className='mb-0'><b>Rp. {convertCurrency(this.state.harga) }</b></span>
                        </div>
                        <div className='col-sm-3  align-self-end'>
                            {this.state.ep === '' ? 
                                <button style={style.btGreen} className='btn mt-4 rounded-0' disabled>Beli</button>
                            :
                                <button data-toggle="modal" data-target="#tokenListrik" style={style.btGreen} className='btn mt-4 rounded-0'>Beli</button>
                            }
                        </div>
                    </div>

                    {/* <!-- Modal --> */}
                    <div className="modal fade" id="tokenListrik" data-backdrop="static" tabIndex="-1" role="dialog" aria-labelledby="tokenListrikLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                            <div className="modal-header" style={{backgroundColor:'#005005',color:'white'}}>
                                <h5 className="modal-title" align='center' id="tokenListrikLabel">INFORMASI PEMBELIAN</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span style={{color:'white'}} aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body pb-0">
                                <div className='row'>
                                    <div className='col'>{this.state.name} </div>
                                    <div className='mr-2'>Logo</div>
                                </div>
                                <div className='row mt-5'>
                                    <div className='col'>Token Lisrik :</div>
                                    <div className='mr-3'> {this.state.nomorPelanggan} </div>
                                </div>
                                <div className='row mt-3 mb-3'>
                                    <div className='col'>Nomor Hp :</div>
                                    <div className='mr-3'> {this.state.nomor} </div>
                                </div>
                                
                                <hr className="my-1"></hr>

                                <div className='row' style={{color:'#2E7D32'}}>
                                    <div className='col'>Bonus :</div>
                                    <div className='mr-3'> {this.state.ep} </div>
                                </div>

                                <hr className="my-1"></hr>
                                <div className='row mt-0 pt-2 pb-2' style={{backgroundColor:'#B7D5C1'}}>
                                    <div className='col'>Jumlah Tagihan :</div>
                                    <div className='mr-3'> {convertCurrency(this.state.harga) } </div>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn  btn-outline-success" data-dismiss="modal">Batalkan</button>
                                <button type="button" onClick={this.handleSubmit}  data-dismiss="modal" className="btn btn-success">Ya, Lanjutkan</button>
                            </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) =>({
    profile : state.riwayatTransaksi.profile.data[0]
})

const mapDispatchToProps  = (dispatch) => ({
    changeReport : (data)=> dispatch({ type : 'REPORT_TRANSACTION', value : data  })
})

// export default Pulsa
export default connect(mapStateToProps, mapDispatchToProps)(Tokenlisrik)

const style = {
    btGreen:{
        fontSize:'14px',
        color:'#FFFFFF',
        backgroundColor:'#2E7D32',
        width: '150px'
      }
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);
const GreenFormControl = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(FormControl);