import React, { Component } from 'react'
import PengaturanProfil from './beranda/menus/pengaturanProfil'
import PengaturanPassword from './beranda/menus/pengaturanPassword'
import PengaturanPin from './beranda/menus/pengaturanPin'
import LoadingPage from './loadingPage'
import "../assets/responsivePage.css"
// import {API,ACCOUNT_PROFILE,APLUS,X_API_KEY} from '../api/index'
// import axios from 'axios'
import Swal from 'sweetalert2'
import { connect } from 'react-redux'
 
class Profil extends Component {
    constructor(){
        super()
        this.state={
            // profileHidden:true,
            // settingPasswordHidden:true,
            // settingPINHidden:true,
            showElement:'Profil',
            isLogin:'',
            loading:true,
            profil:[]
        }
    }
    logout(){
        localStorage.removeItem('isLogin')
        window.location = '/'
    }
    noLogin(){
        Swal.fire({
            title: 'Session Expired',
            text: "Anda Terdekteksi Login Di device lain, silahkan Login kembali",
            icon: 'warning',
            showCancelButton: false,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Ya',
            allowOutsideClick: false,
        }).then((result) => {
            this.logout()
        })
    }
    confirmLogout(){
        Swal.fire({
            title: 'Log Out',
            text: "You Will Bw Returned To Login Page",
            icon: 'question',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Logout',
            allowOutsideClick: false,
        }).then((result) => {
            if (result.value) {
                this.logout()
            }
        })

    }
    // profileHidden() {
    //     this.setState({
    //         profileHidden: !this.state.profileHidden,
    //     })
    // }
    // settingPasswordHidden() {
    //     this.setState({
    //         settingPasswordHidden: !this.state.settingPasswordHidden,
    //     })
    // }
    // settingPINHidden() {
    //     this.setState({
    //         settingPINHidden: !this.state.settingPINHidden
    //     })
    // }
    componentDidMount(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        this.setState({isLogin:isLogin})
        // console.log(isLogin)
        if (!isLogin){
            window.location = '/'
        } else {
            this.setState({ loading:false });
        }
    }
    render() {
        if(this.state.profil === null){
            this.noLogin()
        }
        if(this.state.loading === true){
            return(
                <LoadingPage/>
            )
        }
        // const {isLogin} = this.state
        const profil = this.props.data.profile
        return (
            <div>
                { this.props.data.profile.data[0] === undefined ? "" :
                <div className='row mt-3'>
                    <div className='container'> 
                        <div className="row m-0 pb-3">
                            <div className='col-sm-4 p-0'>
                                <div  style={{boxShadow:'0px 0px 6px #00000029', backgroundColor:'#FFFFFF'}}>
                                    <p align='center' className='' style={{backgroundColor:'#2E7D32',paddingTop:'10px',marginBottom:'0px', height:'40px',color:'white',fontSize:'8pt'}}>Detail</p>
                                    <ul className="list-group">
                                        <li className="list-group-item rounded-0 p-2">
                                            <p className='mb-0 profil-font' style={{fontSize:'10pt'}}>Ekl ID</p>
                                            <p className='mb-0 profil-font' style={{fontSize:'10pt'}}> {profil.userID} </p>
                                        </li>
                                        <li className="list-group-item rounded-0 p-2">
                                            <p className='mb-0 profil-font' style={{fontSize:'10pt'}}>Email</p>
                                            <p className='mb-0 profil-font' style={{fontSize:'10pt'}}> {profil.data[0].o_mail} </p>
                                        </li>
                                        <li className="list-group-item rounded-0 p-2">
                                            <p className='mb-0 profil-font' style={{fontSize:'10pt'}}>Telepon</p>
                                            <p className='mb-0 profil-font' style={{fontSize:'10pt'}}> {profil.data[0].o_hp} </p>
                                        </li>
                                        <li className="list-group-item rounded-0 p-2">
                                            <p className='mb-0 profil-font' style={{fontSize:'10pt'}}>Jabatan</p>
                                            <p className='mb-0 profil-font' style={{fontSize:'10pt'}}> {profil.data[0].o_jabatanmember} </p>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div className='col-sm-8 menuProfil'>
                                <div className='mb-1'  style={styles.boxShadow}> 
                                    <div className='row'>
                                        <div className='col-sm-8'>
                                            <p className='p-3 mb-0' style={{fontSize:'10pt',color:'#787474'}}>Detile Profil</p>
                                        </div>
                                        {this.state.showElement === 'Profil' ? 
                                            <div onClick={()=>this.setState({showElement:''})} className='col-sm-4 p-3 pr-5 mb-0' align='right'>
                                                <div style={{cursor:'pointer'}} className='rounded-circle'>
                                                    <i style={{color:'#005005'}} className="fa fa-pencil"></i>
                                                </div>
                                            </div>
                                        : 
                                            <div onClick={()=>this.setState({showElement:'Profil'})} className='col-sm-4 p-3 pr-5 mb-0' align='right'>
                                                <div style={{cursor:'pointer'}} className='rounded-circle'>
                                                    <i style={{color:'black'}} className="fa fa-pencil"></i>
                                                </div>
                                            </div>
                                        }
                                    </div>
                                </div> 
                                <div style={this.state.showElement === 'Profil' ? {display:'block'} : {display:'none'} }>
                                    <PengaturanProfil/>  
                                    {/* <div className='row p-3'>
                                        <div className='col-sm-3'></div>
                                        <div className='col-sm-3'></div>
                                        <div className='col-sm-3'></div>
                                        <div className='col-sm-3' onClick={()=>this.setState({showElement:''})} >
                                            <p style={styles.btnWhite} align='center'>Tutup Halaman</p>
                                        </div>
                                    </div> */}
                                </div>
                                <div className='mb-1' style={styles.boxShadow}> 
                                    <div className='row'>
                                        <div className='col-sm-8'>
                                            <p className='p-3 mb-0' style={{fontSize:'10pt',color:'#787474'}}>Pengaturan Password</p>
                                        </div>
                                        {this.state.showElement === 'Password' ? 
                                            <div onClick={()=>this.setState({showElement:''})}className='col-sm-4 p-3 pr-5 mb-0' align='right'>
                                                <div style={{cursor:'pointer'}} className='rounded-circle'>
                                                    <i className="fa fa-pencil"  style={{color:'#005005'}}></i>
                                                </div>
                                            </div>
                                        :
                                            <div onClick={()=>this.setState({showElement:'Password'})}className='col-sm-4 p-3 pr-5 mb-0' align='right'>
                                                <div style={{cursor:'pointer'}} className='rounded-circle'>
                                                    <i className="fa fa-pencil"  style={{color:'black'}}></i>
                                                </div>
                                            </div>
                                        }
                                    </div>
                                </div> 
                                <div  style={this.state.showElement === 'Password' ? {display:'block'} : {display:'none'} }>
                                    <PengaturanPassword/>
                                </div>
                                <div className='mb-1' style={styles.boxShadow}> 
                                    <div className='row'>
                                        <div className='col-sm-8'>
                                            <p className='p-3 mb-0' style={{fontSize:'10pt',color:'#787474'}}>Pengaturan PIN</p>
                                        </div>
                                        {this.state.showElement === 'Pin' ? 
                                            <div onClick={()=>this.setState({showElement:''})} className='col-sm-4 p-3 pr-5 mb-0' align='right'>
                                                <div style={{cursor:'pointer'}} className='rounded-circle'>
                                                    <i className="fa fa-pencil" style={{color:'#005005'}}></i>
                                                </div>
                                            </div>
                                        : 
                                            <div onClick={()=>this.setState({showElement:'Pin'})} className='col-sm-4 p-3 pr-5 mb-0' align='right'>
                                                <div style={{cursor:'pointer'}} className='rounded-circle'>
                                                    <i className="fa fa-pencil" style={{color:'black'}}></i>
                                                </div>
                                            </div>
                                        }
                                    </div>
                                </div> 
                                <div style={this.state.showElement === 'Pin' ? {display:'block'} : {display:'none'} }>
                                    <PengaturanPin/>
                    
                                </div>
                            </div>  
                        </div>
                    </div>
            </div>
            } 
        </div>
        )
    }
} 



const mapStateToProps = (state) => ({
    data:state.riwayatTransaksi
})
// const mapDispatchToProps = dispatch => ({
//     showDetileProfile:
// })
export default connect(mapStateToProps,null)(Profil)

const styles={
    boxShadow:{
        boxShadow:'0px 0px 6px #00000029',
        backgroundColor:'#FFFFFF'
    },
    btnLogout:{
        backgroundColor:'transparent',
        fontSize:'10pt',
        color:'white',
        cursor:'pointer',
        border:'2px solid white',
        padding:'10px'
    },
    btnNonaktif:{
        backgroundColor:'#2E7D32',
        fontSize:'10pt',
        color:'white',
        cursor:'pointer',
        border:'2px solid #2E7D32',
        padding:'10px'
    },
    btnWhite:{
        fontSize:'12px', color:'#2E7D32', backgroundColor:'white', width: '150px',height:'40px',boxShadow: '0px 0px 6px #00000029',paddingTop:'10px',cursor:'pointer'
    },
}