import React, { Component } from 'react'
import DepositPage from './RiwayatDeposit/DepositPage'
import TransaksiPage from './RiwayatTransaksi/TransaksiPage'
import { connect } from 'react-redux'
import TransferPage from './RiwayatTransfer/TransferPage'
import SaldoPage from './RiwayatSaldo/SaldoPage' 
import BonusPage from './RiwayatBonus/BonusPage' 
import PenarikanPage from './RiwayatPenarikan/PenarikanPage' 

class DetileRiwayat extends Component {
    constructor(){
        super()
        this.state={
            loading:true,
            history : '',
            list: 'Saldo',
            search:'off',
            
        }
    }

    openSearch(data){
        this.setState({search:data})
    }

    componentDidMount() {
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        this.setState({isLogin:isLogin})
        
        if (!isLogin){
            window.location = '/'
        } else {
        }
    }

    render() {
        const riwayat = this.props.riwayat
        const profil = this.props.profil
        // console.log(this.props.profil)
        return (
            <div>
                <div className='row mt-4 mb-4 pb-4' style={{color:'#827D7D', fontSize:'12px', borderBottom:'0.5px solid #D6D6D6 '}}>
                    <div className='col-sm-6'>
                        <table>
                            <tbody>
                                <tr>
                                    <td>Nama Lengkap</td>
                                    <td>:</td>
                                    <td> { profil.data[0].o_nama_member } </td>
                                </tr>
                                <tr>
                                    <td>ID EKL</td>
                                    <td>:</td>
                                    <td> { profil.userID } </td>
                                </tr>
                                <tr>
                                    <td>Jabatan</td>
                                    <td>:</td>
                                    <td> { profil.data[0].o_jabatanmember } </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                    <div className='col-sm-6'></div>
                </div>
                <div className='row mt-4 mb-4 pb-4' style={{fontSize:'12px',color:'#827D7D'}}>
                    <div className='col-sm-12'>
                        {/* <label className="switch">
                            {this.state.search === 'off' ? 
                                <input type='checkbox' onClick={()=> this.openSearch('on')} />
                            :
                                <input type='checkbox' defaultValue onClick={()=> this.openSearch('off')} />
                            }
                            <span className="slider round"></span>
                        </label>
                        <span className='ml-2'>Pencarian Berdasarkan Tanggal</span> */}
                    </div>
                    <div className='col-sm-12'>
                        { riwayat === 1 ? <TransaksiPage/> : <div></div> }
                        { riwayat === 2 ? <DepositPage/> : <div></div> }
                        { riwayat === 3 ? <SaldoPage/> : <div></div> }
                        { riwayat === 4 ? <PenarikanPage/> : <div></div> }
                        { riwayat === 5 ? <BonusPage/> : <div></div> }
                        { riwayat === 6 ? <TransferPage/> : <div></div> }
                        
                    </div>
                    <div className='col-sm-12'>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) =>({
    riwayat : state.firstReducer.kodeRiwayat,
    profil : state.riwayatTransaksi.profile
})

export default connect(mapStateToProps, null)(DetileRiwayat)