import React from 'react';
import {convertCurrency} from '../../../payment/commons/commonLib'

const DataTables = ({ posts, loading }) => {
  if (loading) {
    return (
        <tbody>
            <tr> 
                <td colSpan='5' align='center'> 
                    Loading...
                </td>
            </tr>
        </tbody>);
  }
// console.log(posts)
  return (
    <tbody>
      {!posts[0] || posts === 'Riwayat Ti' ? 
        <tr> 
            <td colSpan='5' align='center'> 
                Data Tidak Ada
            </td>
        </tr>
      : 
        posts.map(posts => (
            <tr key={posts.tanggal}> 
                <td>{posts.tanggal}</td>
                <td>{posts.pengirim}</td>
                <td>{posts.penerima}</td>
                <td> Rp.  { convertCurrency(posts.nilai) }</td>
                {/* <td>{posts.status_deposit}</td> */}
                {/* <td> View </td> */}
            </tr>
        ))
      }
    </tbody>
  );
};

export default DataTables;