import React, { Component } from 'react'
// import '../TransferSaldo/style.css'
import {TextField,withStyles } from '@material-ui/core';
import{ connect } from 'react-redux'
import { convertCurrency } from '../../../payment/commons/commonLib'
import { API,APLUS,X_API_KEY,GET_OTP,WITHDRAW } from '../../../../api/index'
import axios from 'axios';
import Swal from 'sweetalert2'

class WithdrawPage extends Component {
    constructor(){
        super()
        this.state={
            nominal:'',
            OTPCode:'',
            pin:'',
            cekOTP:false,
            loading:true,
            response:true
        }
        this.handleChange = this.handleChange.bind(this)
    }
    handleChange(e){
        this.setState({[e.target.name]:e.target.value})
    }
    reqOTP(){
        this.setState({loading:false})
        const header={
            'X-Api-Key' : X_API_KEY
        }
        const body ={
            'mobilePhone':this.props.data.profile.data[0].o_hp,
            'aplUse' : APLUS,
            'type_otp' : 'REQUEST WITHDRAW'
        }
        axios.post(API+GET_OTP, body, {headers:header})
        .then(res=>{
            // console.log(res)
            if(res.data.errNumber !== '0'){
                Swal.fire({
                    icon : 'error',
                    title: res.data.status,
                    text : res.data.respMessage
                })
            }else{
                Swal.fire({
                    icon : 'success',
                    title: 'Request OTP Success',
                    text : 'Request OTP berhasil dikirim ke nomor   '+res.data.userID
                })
                this.setState({cekOTP:true,loading:true})
            }
        })
    }
    getWithdraw(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        const header={
            'X-Api-Key' : X_API_KEY
        }

        const body ={
            'userID' : isLogin.userID,
            'aplUse':APLUS,
            'accessToken' : isLogin.mbr_token,
            'amount' : this.state.nominal,
            'key' : this.state.OTPCode,
            'pin': this.state.pin
        }
        axios.post(API+WITHDRAW, body, {headers:header})
        .then(res=>{
            // console.log(res)
            if(res.data.errNumber !== '0'){
                Swal.fire({
                    icon : 'error',
                    title: res.data.status,
                    text : res.data.respMessage
                })
            }else{
                this.setState({response:false})
            }
        })

    }

    render(){
        // console.log(this.state.response)
        return (
            <div>
                {this.state.response === true ? 
                <div className='fixed-top rounded' style={{marginTop:'80px'}}>
                    <div className='konfirmasi '>
                        <div className="modalDialog">
                            <div>
                                <div className='row m-0'>
                                    <div className='col-sm-12'  style={{borderBottom:'1px solid #707070',color:'#707070'}}>
                                        <p className='mt-3 mb-3'><b> Penarikan Saldo Transaksi </b></p>
                                    </div>
                                </div>
                                <div className='row mt-2 m-0'>
                                    <div className='col-sm-12'>
                                        <p className='m-0 p-0' style={{ fontSize:'10pt',color:'black'}}> Saldo Total </p>
                                        <p className='m-0 p-0' style={{ fontSize:'10pt',color:'#2E7D32',borderBottom:'1px solid #2E7D32'}}> Rp. {convertCurrency(this.props.data.saldo.bonus_member)} </p>
                                        
                                    </div>
                                    <div className='col-sm-12 mt-3'>
                                        <GreenTextField label="Masukkan Nominal" style={{width:'100%'}}
                                            autoComplete='off' 
                                            name="nominal" 
                                            value={this.state.nominal} 
                                            onChange={this.handleChange}
                                        />
                                        <span  style={style.validate}>Minimal isi ulang adalah Rp. 10.000</span>
                                    </div>
                                    <div className='col-sm-12 mt-2'>
                                        <GreenTextField label="OTP Code" style={{width:'100%'}}
                                            autoComplete='off' 
                                            name="OTPCode" 
                                            value={this.state.OTPCode} 
                                            onChange={this.handleChange}
                                        />
                                        <span  style={style.validate}>Klik Request OTP untuk mendapatkan OTP</span>
                                    </div>
                                    <div className='col-sm-12 mt-2'>
                                        <GreenTextField label="PIN" style={{width:'100%'}}
                                            autoComplete='off' 
                                            name="pin" 
                                            value={this.state.pin} 
                                            onChange={this.handleChange}
                                        />
                                    </div>
                                </div>
                                <div className='row mt-4 m-0'>
                                    <div className='col-sm-12 mb-2 mt-2'>
                                        {/* { this.state.IDTujuan === '' ? 
                                            <button className='btn btn-primary mr-2 btn-sm disabled'>Lanjutkan Transaksi</button>
                                        :
                                            <button className='btn btn-primary mr-2 btn-sm'>Lanjutkan Transaksi</button>
                                        } */}
                                        {this.state.cekOTP === false ? 
                                            <button className='btn btn-success btn-sm mr-2  disabled'>Withdraw</button>
                                        :
                                            <button className='btn btn-success btn-sm mr-2' onClick={()=>this.getWithdraw()} >Withdraw</button>
                                        }
                                        {this.state.loading === true ?
                                            <button className='btn btn-info btn-sm mr-2' onClick={() => this.reqOTP()}>Request OTP</button>
                                        :
                                            <button className='btn btn-info btn-sm mr-2'>Loading</button>
                                        }
                                            <button className='btn btn-secondary btn-sm' onClick={() => this.props.setStatusModal('close')}>Batal</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                :
                <div className='fixed-top rounded' style={{marginTop:'80px'}}>
                    <div className='konfirmasi '>
                        <div className="modalDialog">
                            <div>
                                <div className='row justify-content-center'>
                                    <div className='mt-4'>
                                        <img src={require('../../../../images/alert/alertSuccess.svg')} alt='success' />
                                    </div>
                                </div>
                                <p align='center' className='mt-2' style={{color:'#2E7D32',fontSize:'10pt'}}>Penarikan Saldo Berhasil</p>
                                <div className='row m-0 mt-5'>
                                    <div className='col-sm-12'>
                                        <div className='row m-0' style={{color:'#2E7D32'}}>
                                            <div className='col-sm-4 pl-0'><b>Total Withdraw</b></div>
                                            <div className='col-sm-4'>:</div>
                                            <div className='col-sm-4'>Rp. {convertCurrency(this.state.nominal)}</div>
                                        </div>
                                        <hr/>
                                        <div className='row m-0 mt-3'>
                                            <div className='col-sm-4 pl-0'><b>Bonus Balance</b></div>
                                            <div className='col-sm-4'>:</div>
                                            <div className='col-sm-4'>Rp. {convertCurrency(this.state.nominal)}</div>
                                        </div>
                                    </div>
                                </div>
                                <div className='row m-0 mt-5'>
                                    <div className='col-sm-12 mb-2 mt-2'>
                                        <button className='btn btn-success pl-4 pr-4 btn-sm' onClick={() => this.props.setStatusModal('close')}>Ok</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            }
            </div>
        )
    }
}


const mapDispatchToProps = dispatch =>({
    setStatusModal : (data) => dispatch({type:'SET_STATUS_MODAL', value:''}),

})

const mapStateToProps = (state) =>({
    data: state.riwayatTransaksi
})

export default connect(mapStateToProps,mapDispatchToProps)(WithdrawPage)

const style = {
    validate:{
        fontSize:'8pt',
        color:'#DB0C0B'
    }
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);
// export default WithdrawPage