import React, { Component } from 'react'
import {convertCurrency} from '../commons/commonLib'
import '../../../style.css'

import AERIA from '../../../images/voucherGame/aeria.png'
import cherry from '../../../images/voucherGame/cherry.png'
import FBCredit from '../../../images/voucherGame/fbCredit.png'
import gameschool from '../../../images/voucherGame/gameschool.png'
import garena from '../../../images/voucherGame/garena.png'
import Lyto from '../../../images/voucherGame/lyto.png'
import mcoin from '../../../images/voucherGame/mcoin.png'
import megaxux from '../../../images/voucherGame/megaxux.png'
import mobilelegend from '../../../images/voucherGame/mobileLegend.png'
import Mol from '../../../images/voucherGame/mol.png'
import playstore from '../../../images/voucherGame/playstore.png'
import PUBG from '../../../images/voucherGame/pubg.png'
import steam from '../../../images/voucherGame/steam.png'
import travian from '../../../images/voucherGame/travian.png'
import unipin from '../../../images/voucherGame/unipin.png'
import wave from '../../../images/voucherGame/wave.png'

import {TextField,InputLabel,Select,FormControl,withStyles } from '@material-ui/core';
import axios from 'axios'
import { Redirect } from 'react-router-dom'
import {API,GAME_PRODUCT,APLUS,X_API_KEY,ORDER_PRODUCT} from '../../../api/index'
import Swal from "sweetalert2"
import LoadingPage from '../../loadingPage'
import { connect } from 'react-redux'

class Game extends Component {
    constructor(){
        super()
        this.state={
            product:[],
            game:[],
            nomor:'',
            nominal:'',
            // harga:'',
            ep : '',
            name:'',
            code :'',
            logo:'',
            title:'',
            list: 'AERIA',
            loading: true,
            redirect:'',
            tipeProduct:'Voucher Game'
        }
        this.handleChangeNomor= this.handleChangeNomor.bind(this);
        this.handleChange= this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e){
        this.setState({loading:true})
        e.preventDefault();
        const {nomor, code} = this.state
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        const header={
            'X-Api-Key' : X_API_KEY,
            // 'User-Agent' : USER_AGENT
        }
        const body = {
            'userID' :isLogin.userID,
            'accessToken' : isLogin.mbr_token,
            'aplUse': APLUS,
            'MSISDN':nomor,
            'productCode':code
        }
        // console.log(body)
        axios.post(API+ORDER_PRODUCT, body, {headers:header} )
            .then(res => {
                const order = res.data;
                this.setState({ order });
                if(order.errNumber !== '0' ){
                    Swal.fire({
                        icon : 'error',
                        title: order.status,
                        text : order.respMessage
                    })
                    this.setState({loading: false})
                }else{
                    this.setState({loading: false})
                    Swal.fire({
                        icon : 'success',
                        title: order.status,
                        text : order.respMessage,
                        allowOutsideClick: false,
                    }).then((result) => {
                        if (result.value) {
                            const keterangan ={
                                'product' : this.state.tipeProduct,
                                'tipe' : this.state.tipe,
                                'ep': this.state.ep,
                                'logo':this.state.logo
                            }
                            const data = {order,keterangan}
                            this.props.changeReport(data)
                            this.setState({ redirect:true })
                        }
                    })
                }
                })
        .catch(error => {
            console.log(error.response)
        });
    }
    handleChange(e){
        this.setState({[e.target.name]:e.target.value})
        const nm = this.state.product.filter(
            (p) => {
                return p.code.indexOf(e.target.value) !== -1;
            }
        )
        // console.log(nm)
        this.setState({harga: nm[0].price})
        this.setState({ep: nm[0].ep})
        this.setState({name: nm[0].name})
        this.setState({code: nm[0].code})
    }
    handleChangeNomor(e){
        this.setState({[e.target.name]:e.target.value})
    }
    handleLayanan(data){
        const game = this.state.product.filter(
            (p) => {
                return p.provider.indexOf(data) !== -1;
            }
        )
        if(this.state.list !== data){
            this.setState({ list : data })
        }
        // console.log(game)
        this.setState({game:game})
        this.setState({title:data})
        if(data === 'AERIA' ){
            this.setState({logo:AERIA})
        }else if(data === "CHERRY"){
            this.setState({logo:cherry})
        }else if(data === "FB KREDIT"){
            this.setState({logo:FBCredit})
        }else if(data === "GARENA"){
            this.setState({logo:garena})
        }else if(data === "GEMSCOOL"){
            this.setState({logo:gameschool})
        }else if(data === "GOOGLE PLAY ID"){
            this.setState({logo:playstore})
        }else if(data === "LYTO"){
            this.setState({logo:Lyto})
        }else if(data === "MCOIN"){
            this.setState({logo:mcoin})
        }else if(data === "MEGAXUS"){
            this.setState({logo:megaxux})
        }else if(data === "MOBILE LEGENDS"){
            this.setState({logo:mobilelegend})
        }else if(data === "MOL"){
            this.setState({logo:Mol})
        }else if(data === "PUBG"){
            this.setState({logo:PUBG})
        }else if(data === "STEAM"){
            this.setState({logo:steam})
        }else if(data === "TRAVIAN"){
            this.setState({logo:travian})
        }else if(data === "UNIPIN"){
            this.setState({logo:unipin})
        }else{
            this.setState({logo:wave})
        }
    }
    componentDidMount(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        if (isLogin){
            const header={
                'X-Api-Key' : X_API_KEY
            }
            const body ={
                'userID' : isLogin.userID,
                'aplUse':APLUS,
                'accessToken' : isLogin.mbr_token
            }
            axios.post(API+GAME_PRODUCT, body, {headers:header} )
            .then(res => {
                const product = res.data.data;
                this.setState({ product });
                this.handleLayanan('AERIA')
                this.setState({loading: false})
                // console.log('product',product)
                })
            .catch(error => {
            });
        }else{
            window.location = '/'
        }
        
    }
    render() {
        if(this.state.redirect === true){
            return <Redirect to='/home/detiletransaksi' />
        }
        if(this.state.loading === true){
            return(
                <LoadingPage/>
            )
        }
        // console.log(this.state.nomor + this.state.server)
        return (
            <div>
                <div className='border shadow p-3 mt-3 mb-3'>
                    <div>
                        <div className="row">
                            <div className='col-sm-12'>
                                <p style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} >Pilih Jenis Layanan</p>
                            </div>
                            <div id='game' className='col-sm-12 detile-product'>
                                <div onClick={() => this.handleLayanan('AERIA')} 
                                     className={this.state.list === 'AERIA' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={AERIA} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Aeria</p>
                                </div>
                                <div onClick={() => this.handleLayanan('CHERRY')} 
                                     className={this.state.list === 'CHERRY' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={cherry} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Cherry</p>
                                </div>
                                <div onClick={() => this.handleLayanan('FB KREDIT')} 
                                     className={this.state.list === 'FB KREDIT' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={FBCredit} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>FB Credit</p>
                                </div>
                                <div onClick={() => this.handleLayanan('GARENA')} 
                                     className={this.state.list === 'GARENA' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={garena} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Garena</p>
                                </div>
                                <div onClick={() => this.handleLayanan('GEMSCOOL')} 
                                     className={this.state.list === 'GEMSCOOL' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={gameschool} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Gameschool</p>
                                </div>
                                <div onClick={() => this.handleLayanan('GOOGLE PLAY ID')} 
                                     className={this.state.list === 'GOOGLE PLAY ID' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={playstore} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Google Play</p>
                                </div>
                                <div onClick={() => this.handleLayanan('LYTO')} 
                                     className={this.state.list === 'LYTO' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={Lyto} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Lyto</p>
                                </div>
                                <div onClick={() => this.handleLayanan('MCOIN')} 
                                     className={this.state.list === 'MCOIN' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={mcoin} width='100%' height='60%' alt='logo' className='mt-2' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Mcoin</p>
                                </div>
                                <div onClick={() => this.handleLayanan('MEGAXUS')} 
                                     className={this.state.list === 'MEGAXUS' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={megaxux} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Megaxus</p>
                                </div>
                                <div onClick={() => this.handleLayanan('MOBILE LEGENDS')} 
                                     className={this.state.list === 'MOBILE LEGENDS' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={mobilelegend} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Mobile Legends</p>
                                </div>
                                <div onClick={() => this.handleLayanan('MOL')} 
                                     className={this.state.list === 'MOL' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={Mol} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Mol</p>
                                </div>
                                <div onClick={() => this.handleLayanan('PUBG')} 
                                     className={this.state.list === 'PUBG' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={PUBG} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>PUBG</p>
                                </div>
                                <div onClick={() => this.handleLayanan('STEAM')} 
                                     className={this.state.list === 'STEAM' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={steam} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Steam</p>
                                </div>
                                <div onClick={() => this.handleLayanan('TRAVIAN')} 
                                     className={this.state.list === 'TRAVIAN' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={travian} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Travian</p>
                                </div>
                                <div onClick={() => this.handleLayanan('UNIPIN')} 
                                     className={this.state.list === 'UNIPIN' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={unipin} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Unipin</p>
                                </div>
                                <div onClick={() => this.handleLayanan('WAVE')} 
                                     className={this.state.list === 'WAVE' ? 'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image'>
                                        <img src={wave} width='85px' className='mt-3' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Wave</p>
                                </div>
                            </div>
                            
                            {/* Etoll */}
                            <div className="col-sm-12" id="Esaldo">
                                <hr className="my-1"></hr>
                                <div className="row">
                                    <div className='col'>
                                        <h5 style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} > {this.state.title} </h5>
                                    </div>
                                    <div className='mr-3'>
                                        {/* <img src={require('../../../images/EToll/E-Toll-BNI.svg')} width='100px' height='50px' alt='logo' /> */}
                                        <img src={this.state.logo} width='110px' alt='logo' />
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className='col-sm mt-2' >
                                        <GreenTextField label="User ID" style={{width:'100%'}} 
                                            name="nomor" 
                                            value={this.state.nomor} 
                                            onChange={this.handleChangeNomor}
                                            autoComplete='off'
                                            // onInput = {(e) =>{
                                            //     e.target.value = Math.max(0, parseInt(e.target.value) ).toString().slice(0,10)
                                            // }}
                                        />
                                        <span style={{fontSize:'9px',color:'#D41C24'}}>Masukkan ID game dan ID server</span>
                                    </div>
                                    <div className='col-sm mt-2' >
                                        <GreenFormControl style={{width:'100%'}}>
                                            <InputLabel htmlFor="nominal-native-simple">Nominal</InputLabel>
                                            <Select
                                                native
                                                value={this.state.nominal}
                                                onChange={this.handleChange}
                                                name="nominal"
                                            >
                                                <option value="" ></option>
                                                {this.state.game.map(product=>(
                                                    <option className='mt-2' value={product.code} key={product.code}>{product.name}</option>
                                                ))}
                                            </Select>
                                        </GreenFormControl>
                                    </div>
                                    <div className='col-sm mt-2' >
                                        <GreenTextField label="Point" style={{width:'100%'}} 
                                            disabled value={this.state.ep}
                                        />
                                    </div>
                                </div>
                                <div className='row  mt-3'>
                                    <div className='col-sm-9'>
                                        <p style={{fontSize:'12px', color:'#707070',width:'100px'}} className='mb-0'>Harga</p>
                                        <span style={{fontSize:'36px', color:'#2E7D32',width:'200px'}} className='mb-0'><b>Rp. {convertCurrency(this.state.harga) }</b></span>
                                    </div>
                                    <div className='col-sm-3  align-self-end'>
                                        {this.state.ep === '' ? 
                                            <button style={style.btGreen} className='btn mt-4 rounded-0' disabled>Beli</button>
                                        :
                                            <button data-toggle="modal" data-target="#games" style={style.btGreen} className='btn mt-4 rounded-0'>Beli</button>
                                        }
                                    </div>
                                </div>
                            </div>
                            {/* End egames */}
                       
                        </div>
                    </div>
                </div>
                
                    {/* <!-- Modal --> */}
                    <div className="modal fade" id="games" data-backdrop="static" tabIndex="-1" role="dialog" aria-labelledby="gamesLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                            <div className="modal-header" style={{backgroundColor:'#005005',color:'white'}}>
                                <h5 className="modal-title" align='center' id="gamesLabel">INFORMASI PEMBELIAN</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span style={{color:'white'}} aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body pb-0">
                                <div className='row'>
                                    <div className='col mt-2'> {this.state.name} </div>
                                    <div className='mr-2'><img src={this.state.logo} width='100px' height='50px' alt='logo'  /></div>
                                </div>
                                <div className='row mt-5'>
                                    <div className='col'>Nomor Pelanggan :</div>
                                    <div className='mr-3'> {this.state.nomor} </div>
                                </div>
                                <hr className="my-3"></hr>
                                <div className='row' style={{color:'rgb(28, 145, 245)'}}>
                                    <div className='col'>Bonus :</div>
                                    <div className='mr-3'> {this.state.ep} </div>
                                </div>
                                <hr className="mt-3 mb-0"></hr>
                                <div className='row mt-0 pt-2 pb-2' style={{backgroundColor:'rgb(48, 144, 54)'}}>
                                    <div className='col'>Jumlah Tagihan :</div>
                                    <div className='mr-3'> {convertCurrency(this.state.harga) } </div>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn  btn-outline-success" data-dismiss="modal">Batalkan</button>
                                <button type="button" onClick={this.handleSubmit}  data-dismiss="modal" className="btn btn-success">Ya, Lanjutkan</button>
                            </div>
                            </div>
                        </div>
                    </div>
            </div>
        )
    }
}


const mapDispatchToProps  = (dispatch) => ({
    changeReport : (data)=> dispatch({ type : 'REPORT_TRANSACTION', value : data  })
})

// export default Pulsa
export default connect(null, mapDispatchToProps)(Game)

const style = {
    btGreen:{
        fontSize:'14px',
        color:'#FFFFFF',
        backgroundColor:'#2E7D32',
        width: '150px'
      }
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);
const GreenFormControl = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(FormControl);