import React, { Component } from 'react'
import {TextField,withStyles } from '@material-ui/core';

class PengaturanPassword extends Component {
    constructor(){
        super()
        this.state={
            showOldPasword:'no',
            showNewPasword:'no',
            showUlangiPasword:'no',
            showPin:'no',
        }
    }

    render() {
        return (
            <div>
            <div className='mb-1' style={{boxShadow:'0px 0px 6px #00000029', backgroundColor:'#FFFFFF'}}> 
                <p className='pl-2' style={{backgroundColor:'#2E7D32',paddingTop:'10px',marginBottom:'0px', height:'40px',color:'white',fontSize:'8pt'}}>
                    Ubah Password
                </p>
                <div className='row'>
                    <div className='col-sm-6'>
                        <div className='m-2'>
                            <GreenTextField label="Password Lama" style={{marginRight:'-20px',width:'100%'}} 
                                name="nomor" 
                                type={this.state.showOldPasword === 'no' ? 'password' : 'text'}
                                // value={this.state.nomor} 
                                // onChange={this.handleChangeNomor}
                                autoComplete='off' 
                            />
                            {this.state.showOldPasword === 'no' ?
                                <i onClick={()=>this.setState({showOldPasword:'ok'})} style={{cursor:'pointer'}} className="fa mt-4 position-absolute fa-eye-slash"></i>
                            :
                                <i onClick={()=>this.setState({showOldPasword:'no'})} style={{cursor:'pointer',color:'#005005'}} className="fa mt-4 position-absolute fa-eye"></i> 
                            }
                        </div>
                    </div>
                    <div className='col-sm-6'>
                    </div>
                </div>
                <div className='row'>
                    <div className='col-sm-6'>
                        <div className='m-2'>
                            <GreenTextField label="Password Baru" style={{marginRight:'-20px',width:'100%'}} 
                                name="nomor" 
                                type={this.state.showPin === 'no' ? 'password' : 'text'}
                                // value={this.state.nomor} 
                                // onChange={this.handleChangeNomor}
                                autoComplete='off' 
                            />
                            {this.state.showPin === 'no' ?
                                <i onClick={()=>this.setState({showPin:'ok'})} style={{cursor:'pointer'}} className="fa mt-4 position-absolute fa-eye-slash"></i>
                            :
                                <i onClick={()=>this.setState({showPin:'no'})} style={{cursor:'pointer',color:'#005005'}} className="fa mt-4 position-absolute fa-eye"></i> 
                            }
                        </div>
                    </div>
                    <div className='col-sm-6'>
                        <div className='m-2'>
                            <GreenTextField label="Ulangi Password" style={{marginRight:'-20px',width:'100%'}} 
                                name="nomor" 
                                type={this.state.showNewPasword === 'no' ? 'password' : 'text'}
                                // value={this.state.nomor} 
                                // onChange={this.handleChangeNomor}
                                autoComplete='off' 
                            />
                            {this.state.showNewPasword === 'no' ?
                                <i onClick={()=>this.setState({showNewPasword:'ok'})} style={{cursor:'pointer'}} className="fa mt-4 position-absolute fa-eye-slash"></i>
                            :
                                <i onClick={()=>this.setState({showNewPasword:'no'})} style={{cursor:'pointer',color:'#005005'}} className="fa mt-4 position-absolute fa-eye"></i> 
                            }
                        </div>
                    </div>
                </div>
                <div className='row pb-3'>
                    <div className='col-sm-6'>
                        <div className='m-2'>
                            <GreenTextField label="PIN" style={{marginRight:'-20px',width:'100%'}} 
                                name="nomor" 
                                type={this.state.showUlangiPasword === 'no' ? 'password' : 'text'}
                                // value={this.state.nomor} 
                                // onChange={this.handleChangeNomor}
                                autoComplete='off' 
                            />
                            {this.state.showUlangiPasword === 'no' ?
                                <i onClick={()=>this.setState({showUlangiPasword:'ok'})} style={{cursor:'pointer'}} className="fa mt-4 position-absolute fa-eye-slash"></i>
                            :
                                <i onClick={()=>this.setState({showUlangiPasword:'no'})} style={{cursor:'pointer',color:'#005005'}} className="fa mt-4 position-absolute fa-eye"></i> 
                            }
                        </div>
                    </div>
                    <div className='col-sm-6'>
                        <div className='m-2'>
                        </div>
                    </div>
                </div>
                </div>

                <div className='row p-3'>
                    <div className='col-sm-3'></div>
                    <div className='col-sm-3'></div>
                    <div className='col-sm-3'>
                        {/* <p style={styles.btnWhite} align='center'>Batalkan</p> */}
                    </div>
                    <div className='col-sm-3'>
                        <button style={styles.btnGreen}>Simpan Perubahan</button>
                    </div>
                    {/* <div className='col-sm-3'>
                        <button style={styles.btnBlue}>Minta Key</button>
                    </div> */}
                </div>
            </div>
        )
    }
}


export default PengaturanPassword

const styles={
    btnGreen:{
        fontSize:'12px', color:'#FFFFFF', backgroundColor:'#2E7D32', width: '150px',padding:'0px',height:'40px',boxShadow: '0px 0px 6px #00000029'
    },
    btnWhite:{
        fontSize:'12px', color:'#2E7D32', backgroundColor:'white', width: '150px',height:'40px',boxShadow: '0px 0px 6px #00000029',paddingTop:'10px'
    },
    btnBlue:{
        fontSize:'12px', color:'#FFFFFF', backgroundColor:'#2E567D', width: '150px',height:'40px',boxShadow: '0px 0px 6px #00000029'
    },
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);