import React, { Component } from 'react'
import {API,PASCABAYAR_PRODUCT,APLUS,X_API_KEY,PASCABAYAR_CEK,ORDER_PRODUCT_PASCABAYAR} from '../../../api/index'
import axios from 'axios'
import {TextField,withStyles } from '@material-ui/core';
import LoadingPage from '../../loadingPage';
import Swal from 'sweetalert2'
import {convertCurrency} from '../commons/commonLib'
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom'

class telkom extends Component {
    constructor(){
      super()
      this.state={
        cekTagihan :'',
        loading:true,
        product: [],
        nomor:'',
        nomorPelanggan:'',
        resTagihan:[],
        tipeProduct:'Telkom',
        redirect:''
      }
      this.handleChange = this.handleChange.bind(this)
      this.handleSubmit = this.handleSubmit.bind(this)
      this.handleOrder = this.handleOrder.bind(this)
    }

    handleOrder(){
      this.setState({loading: true})
      const {resTagihan} = this.state
      const isLogin = JSON.parse(localStorage.getItem('isLogin'))
      const header={
          'X-Api-Key' : X_API_KEY
      }
      const body = {
          'userID' :isLogin.userID,
          'accessToken' : isLogin.mbr_token,
          'aplUse': APLUS,
          'billingReferenceID' : resTagihan.billingReferenceID
      }
      axios.post(API+ORDER_PRODUCT_PASCABAYAR, body, {headers:header} )
      .then(res => {
          const orderProduct = res.data;
          this.setState({ loading:false });
            if(orderProduct.errNumber !== '0'){
              Swal.fire({
                  icon : 'error',
                  title: orderProduct.status,
                  text : orderProduct.respMessage
              })
              this.setState({loading: false})
            }else{
              Swal.fire({
                  icon : 'success',
                  title: orderProduct.status,
                  text : orderProduct.respMessage,
                  allowOutsideClick: false,
              }).then((result) => {
                  if (result.value) {
                      const keterangan ={
                          'product' : this.state.tipeProduct
                      }
                      const data = {order:this.state.resTagihan,keterangan}
                      this.props.changeReport(data)
                      this.setState({ redirect:true })
                  }
              })
              this.setState({loading: false})
            }
          })
      .catch(error => {
          console.log(error.response)
      });
  }
    handleSubmit(e){
        e.preventDefault();
        this.setState({loading:true})
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        const {product,} = this.state
        const header={
            'X-Api-Key' : X_API_KEY
        }
        const body = {
            'userID' :isLogin.userID,
            'accessToken' : isLogin.mbr_token,
            'aplUse': APLUS,
            'customerID' : this.state.nomorPelanggan,
            'customerMSISDN': this.state.nomor,
            'productCode': product.code
        }
        axios.post(API+PASCABAYAR_CEK, body, {headers:header})
          .then(res => {
            const order = res.data;
            if(order.errNumber !== '0'){
              Swal.fire({
                  icon : 'error',
                  title: order.status,
                  text : order.respMessage
              })
              this.setState({loading: false})
            }else{
              this.setState({
                resTagihan:order,
                loading: false, 
                cekTagihan:'open'
              })
            }
            console.log(order)
          })
          .catch(error => {
            this.setState({loading: false})
              Swal.fire({
                allowOutsideClick: false,
                title: 'Request Tume Out',
                showClass: {
                  popup: 'animated fadeInDown faster'
                },
                hideClass: {
                  popup: 'animated fadeOutUp faster'
                }
              })
          });
        
      }
    handleChange(e){
        this.setState({[e.target.name] : e.target.value})
    }
    componentDidMount(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        if (isLogin){
            const header={
                'X-Api-Key' : X_API_KEY
            }
            const body ={
                'userID' : isLogin.userID,
                'aplUse':APLUS,
                'accessToken' : isLogin.mbr_token,
                'productGroup' : 'TELKOM'
            }
            axios.post(API+PASCABAYAR_PRODUCT, body, {headers:header} )
            .then(res => {
                const product = res.data.productList[0];
                // console.log('product',product)
                this.setState({ product:product, loading:false, nomor:this.props.profile.o_hp });
                })
            .catch(error => {
            });
        }else{
            window.location = '/'
        }
      }
    render() {
      if(this.state.redirect === true){
          return <Redirect to='/home/detiletagihan' />
      }
        const {resTagihan} = this.state
          if(this.state.loading === true){
              return (
                <LoadingPage/>
              )
          }
        return (
            <div>
                <div className=' border shadow p-3 mt-3 mb-3'>
                    <div className="row mt-2">
                        <div className='col-sm-12'>
                            <h3 style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} >Telkom</h3>
                        </div>
                    </div>
                    <div className='row'>
                        <div className='col-sm mt-2' >
                            <GreenTextField label="Nomor Pelanggan" style={{width:'100%'}} 
                                name="nomorPelanggan" 
                                onChange={this.handleChange}
                                value={this.state.nomorPelanggan}
                                autoComplete='off'
                            />
                        </div>
                        <div className='col-sm mt-2' >
                            <GreenTextField label="Nomor Telephone" style={{width:'100%'}} 
                                name="nomor" 
                                onChange={this.handleChange}
                                value={this.state.nomor}
                                autoComplete='off'
                            />
                            <span style={style.validate}>Nomor digunakan untuk menerima konfirmasi</span>
                        </div>
                        <div className='col-sm mt-2'>
                            {this.state.nomor === '' ? 
                                <button style={style.btGreen} className='btn mt-4 rounded-0' disabled>Cek Tagihan</button>
                            :
                                <button onClick={this.handleSubmit} style={style.btGreen} className='btn mt-4 rounded-0'>Cek Tagihan</button>
                            }
                        </div>
                    </div>
                </div>
                {!this.state.cekTagihan ? <div></div> : 
                <div className='border shadow p-3 mt-3 mb-3'>
                    <div className="row mt-2">
                        <div className='col-sm-12'>
                            <h5 style={{color:'#2E7D32'}} >DETAIL TRANSAKSI</h5>
                        </div>
                    </div>
                    <hr className='my-1'></hr>
                    <div className='row mt-2'>
                      <table className='table table-borderless'>
                        <tbody>
                          <tr>
                            <td>Transaction ID</td>
                            <td>:</td>
                            <td>{resTagihan.billingReferenceID}</td>
                          </tr>
                          <tr>
                            <td>ID Customer</td>
                            <td>:</td>
                            <td>{resTagihan.customerID}</td>
                          </tr>
                          <tr>
                            <td>Name</td>
                            <td>:</td>
                            <td>{resTagihan.customerName}</td>
                          </tr>
                          <tr>
                            <td>Phone</td>
                            <td>:</td>
                            <td>{resTagihan.customerMSISDN}</td>
                          </tr>
                          <tr>
                            <td>Date</td>
                            <td>:</td>
                            <td>{resTagihan.respTime}</td>
                          </tr>
                          <tr>
                            <td>Payment</td>
                            <td>:</td>
                            <td>Rp.  {convertCurrency(resTagihan.payment)}</td>
                          </tr>
                          <tr>
                            <td>Admin Bank</td>
                            <td>:</td>
                            <td>Rp. {convertCurrency(resTagihan.adminBank)}</td>
                          </tr>
                          <tr>
                            <td>Billink</td>
                            <td>:</td>
                            <td> Rp. {convertCurrency(resTagihan.billing)}</td>
                          </tr>
                          <tr>
                            <td>Periode</td>
                            <td>:</td>
                            <td>{resTagihan.period}</td>
                          </tr>
                          <tr>
                            <td>Notice</td>
                            <td>:</td>
                            <td>{resTagihan.status}</td>
                          </tr>
                          <tr>
                            <td>Point</td>
                            <td>:</td>
                            <td>{resTagihan.ep}</td>
                          </tr>
                        </tbody>  
                      </table>
                    </div>
                    <div className='row mt-3 ml-0'>
                        <button style={style.btGreen}  data-toggle="modal" data-target="#telkom"  className='btn rounded-0'>PROSES</button>
                    </div>
                </div>
                } 

                {/* <!-- Modal --> */}
                <div className="modal fade" id="telkom" data-backdrop="static" tabIndex="-1" role="dialog" aria-labelledby="telkomLabel" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                        <div className="modal-header" style={{backgroundColor:'#005005',color:'white'}}>
                            <h5 className="modal-title" align='center' id="telkomLabel">INFORMASI PEMBELIAN</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span style={{color:'white'}} aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body pb-0">
                            <div className='row'>
                                <div className='col'>Tanggal :</div>
                                <div className='mr-3'>{resTagihan.respTime}</div>
                                
                            </div>
                            <div className='row mt-4'>
                                <div className='col'>Jumlah Tagihan :</div>
                                <div className='mr-3'> Rp.  {convertCurrency(resTagihan.payment)} </div>
                            </div>
                            <div className='row mt-2'>
                                <div className='col'>Biaya Transaksi :</div>
                                <div className='mr-3'> Rp.  {convertCurrency(resTagihan.adminBank)} </div>
                            </div>
                            <hr className="mt-3 mb-0"></hr>
                            <div className='row mt-0 pt-2 pb-2' style={{backgroundColor:'rgb(48, 144, 54)'}}>
                                <div className='col'>Jumlah Tagihan :</div>
                                <div className='mr-3'>Rp. {convertCurrency(resTagihan.billing) } </div>
                            </div>
                            <div className='row mt-2'>
                                <div className='col' style={{color:'#2E7D32'}}>No Pelanggan :</div>
                                <div className='mr-3' style={{color:'#2E7D32'}}> {resTagihan.customerID} </div>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn  btn-outline-success" data-dismiss="modal">Batalkan</button>
                            <button type="button" onClick={this.handleOrder}  data-dismiss="modal" className="btn btn-success">Ya, Lanjutkan</button>
                        </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) =>({
  profile : state.riwayatTransaksi.profile.data[0]
})

const mapDispatchToProps  = (dispatch) => ({
  changeReport : (data)=> dispatch({ type : 'REPORT_TRANSACTION', value : data  })
})
export default connect(mapStateToProps, mapDispatchToProps)(telkom)

const style = {
    btGreen:{
        fontSize:'14px',
        color:'#FFFFFF',
        backgroundColor:'#2E7D32',
        width: '150px'
      },
    validate:{
      fontSize:'8pt',
      color:'#DB0C0B'
    }
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);