import React from 'react';
import {convertCurrency} from '../../../payment/commons/commonLib'

const DataTables = ({ posts, loading }) => {
  if (loading) {
    return (
        <tbody>
            <tr> 
                <td colSpan='5' align='center'> 
                    Loading...
                </td>
            </tr>
        </tbody>);
  }
// console.log(posts)
  return (
    <tbody>
      {!posts[0] || posts === 'Riwayat Ti' ? 
        <tr> 
            <td colSpan='5' align='center'> 
                Data Tidak Ada
            </td>
        </tr>
      : 
        posts.map(posts => (
            <tr key={posts.tgl_perolehan}> 
                <td>{posts.tgl_perolehan}</td>
                <td><b>Jenis Bonus :</b> {posts.jenis_bonus} <br/> <b>Status :</b> {posts.status_bonus} <br/>  <b>Jumlah Bonus :</b>  Rp.  { convertCurrency(posts.jml_bonus) } </td>
                <td>{posts.keterangan}</td>
            </tr>
        ))
      }
    </tbody>
  );
};

export default DataTables;