import React from 'react';
import {convertCurrency} from '../../../payment/commons/commonLib'

const DataTables = ({ posts, loading }) => {
  if (loading) {
    return (
        <tbody>
            <tr> 
                <td colSpan='5' align='center'> 
                    Loading...
                </td>
            </tr>
        </tbody>);
  }
// console.log(posts)
  return (
    <tbody>
      {!posts[0] || posts === 'Riwayat Ti' ? 
        <tr> 
            <td colSpan='5' align='center'> 
                Data Tidak Ada
            </td>
        </tr>
      : 
        posts.map(posts => (
            <tr key={posts.tgl_mutasi}> 
                <td>{posts.tgl_mutasi}</td>
                <td> Rp.  { convertCurrency(posts.uang_masuk) }</td>
                <td>Rp.  { convertCurrency(posts.uang_keluar) }</td>
                {/* <td>{posts.mutasi_status}</td> */}
                <td> { posts.keterangan } </td>
            </tr>
        ))
      }
    </tbody>
  );
};

export default DataTables;