import './style.css'
import React, { Component } from 'react'
import {TextField,withStyles } from '@material-ui/core';
import { API,X_API_KEY,APLUS,GET_MEMBER,GET_TRANSFER } from '../../../../api/index' 
import{ connect } from 'react-redux'
import axios from 'axios'
import Swal from 'sweetalert2'

class transferSaldo extends Component {
    constructor(){
        super()
        this.state={
            IDTujuan : '',
            nomor: '',
            pin:'',
            nominal:''
        }
        this.handleChange = this.handleChange.bind(this)
    }

    handleChange(event){
        this.setState({[event.target.name] : event.target.value})
    }
    cekIdTujuan(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        if (isLogin){
            const header={
                'X-Api-Key' : X_API_KEY
            }

            const body ={
                'userID' : isLogin.userID,
                'aplUse':APLUS,
                'accessToken' : isLogin.mbr_token,
                'destination' : this.state.nomor
            }
            axios.post(API+GET_MEMBER, body, {headers:header} )
            .then(res => {
                // console.log(res.data)
                    if(res.data.errNumber !== '0'){
                        Swal.fire({
                            title: res.data.status,
                            text: res.data.respMessage,
                            icon: 'error',
                            showCancelButton: false,
                            confirmButtonColor: '#3085d6',
                            cancelButtonColor: '#d33',
                            confirmButtonText: 'Kembali',
                            allowOutsideClick: false,
                          }).then((result) => {
                          })
                    }else{
                        this.setState({IDTujuan : res.data.datarespon[0]})
                    }
                })
            .catch(error => {
                console.log(error.response)
            });
        }else{
            window.location = '/'
        }
    }
    
    requestTransfer(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        if (isLogin){
            const header={
                'X-Api-Key' : X_API_KEY
            }

            const body ={
                'userID' : isLogin.userID,
                'aplUse':APLUS,
                'accessToken' : isLogin.mbr_token,
                'tujuan' : this.state.nomor,
                'nominal' : this.state.nominal,
                'pin' : this.state.pin
            }
            axios.post(API+GET_TRANSFER, body, {headers:header} )
            .then(res => {
                // console.log(res.data)
                    if(res.data.errNumber !== '0'){
                        Swal.fire({
                            title: res.data.status,
                            text: res.data.respMessage,
                            icon: 'error',
                            showCancelButton: false,
                            confirmButtonColor: '#3085d6',
                            cancelButtonColor: '#d33',
                            confirmButtonText: 'Kembali',
                            allowOutsideClick: false,
                          }).then((result) => {
                          })
                    }else{
                        Swal.fire({
                            title: res.data.status,
                            text: res.data.respMessage,
                            icon: 'success',
                            showCancelButton: false,
                            confirmButtonColor: '#3085d6',
                            cancelButtonColor: '#d33',
                            confirmButtonText: 'Kembali',
                            allowOutsideClick: false,
                          }).then((result) => {
                          })
                        //   console.log(res.data)
                    }
                })
            .catch(error => {
                console.log(error.response)
            });
        }else{
            window.location = '/'
        }
    }

    render() {
        // console.log(this.state)
        return (
            <div>
            <div className='fixed-top rounded' style={{marginTop:'80px'}}>
                    <div className='konfirmasi'>
                        <div className="modalDialog">
                            <div>
                                <div className='row m-0'>
                                    <div className='col-sm-12'  style={{borderBottom:'1px solid #707070',color:'#707070'}}>
                                        <p className='mt-3 mb-3'><b>Transfer Saldo OTUChat</b></p>
                                    </div>
                                </div>
                                <div className='row mt-2 m-0'>
                                    <div className='col-sm-12'>
                                        <GreenTextField label="Jumlah Transaksi" style={{width:'100%'}} 
                                            autoComplete='off'
                                            type='number'
                                            name="nominal" 
                                            value={this.state.nominal} 
                                            onChange={this.handleChange}
                                        />
                                        <span  style={style.validate}>Minimal isi ulang adalah Rp. 10.000</span>
                                    </div>
                                </div>
                                <div className='row mt-2 m-0'>
                                    <div className='col-sm-9'>
                                        <GreenTextField label="ID/Nomor OTU Member Tujuan" style={{width:'100%'}}
                                            autoComplete='off' 
                                            name="nomor" 
                                            value={this.state.nomor} 
                                            onChange={this.handleChange}
                                        />
                                        <span  style={style.validate}> Cek tujuan terlebih dahulu sebelum lanjut transaksi </span>
                                    </div>
                                    <div className='col-sm-3 mt-3'>
                                    <button
                                        onClick={()=>this.cekIdTujuan()}
                                        type="button" 
                                        className="btn btn-outline-success btn-sm"
                                    > Cek ID </button>
                                    </div>
                                </div>
                                {this.state.IDTujuan === '' ? '' :
                                    <div className='row mt-2 m-0'>
                                        <div className='col-sm-12 mt-3'  style={{color:'#827D7D', fontSize:'12px'}}>
                                            <table>
                                                <tbody>
                                                    <tr>
                                                        <td style={{color:'black'}}> ID Tujuan </td>
                                                        <td style={{color:'black'}}>:</td>
                                                        <td> {this.state.IDTujuan.id_member} </td>
                                                    </tr>
                                                    <tr>
                                                        <td style={{color:'black'}}>Tujuan Transfer</td>
                                                        <td style={{color:'black'}}>:</td>
                                                        <td> {this.state.IDTujuan.nama_member} </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>

                                    </div>
                                }
                                <div className='row mt-2 m-0'>
                                    <div className='col-sm-12'>
                                        <GreenTextField label="PIN" style={{width:'100%'}} 
                                            autoComplete='off'
                                            name="pin" 
                                            value={this.state.pin} 
                                            onChange={this.handleChange}
                                        />
                                    </div>
                                </div>
                                <div className='row mt-2 m-0'>
                                    <div className='col-sm-12 mb-2 mt-2'>
                                        { this.state.IDTujuan === '' ? 
                                            <button className='btn btn-primary mr-2 btn-sm disabled'>Lanjutkan Transaksi</button>
                                        :
                                            <button className='btn btn-primary mr-2 btn-sm' onClick={() =>this.requestTransfer() }>Lanjutkan Transaksi</button>
                                        }
                                        <button className='btn btn-secondary btn-sm' onClick={() => this.props.setStatusModal('close')}>Batal</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = dispatch =>({
    setStatusModal : (data) => dispatch({type:'SET_STATUS_MODAL', value:''}),

})


export default connect(null,mapDispatchToProps)(transferSaldo)

const style = {
    validate:{
        fontSize:'8pt',
        color:'#DB0C0B'
    }
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);