import React, { Component } from 'react'
import {convertCurrency} from '../commons/commonLib'
import '../../../style.css'
import cinema from '../../../images/ESaldo/Cinema.png'
import dana from '../../../images/ESaldo/Dana.svg'
import gopay from '../../../images/ESaldo/Gopay.svg'
import ovo from '../../../images/ESaldo/ovo.svg'
import link from '../../../images/ESaldo/Link.png'
import tix from '../../../images/ESaldo/Tix-ID.svg'
import {TextField,InputLabel,Select,FormControl,withStyles } from '@material-ui/core';
import axios from 'axios'
import { Redirect } from 'react-router-dom'
import {API,ESALDO_PRODUCT,APLUS,X_API_KEY,ORDER_PRODUCT} from '../../../api/index'
import Swal from "sweetalert2"
import LoadingPage from '../../loadingPage'
import { connect } from 'react-redux'

class Esaldo extends Component {
    constructor(){
        super()
        this.state={
            product:[],
            esaldo:[],
            nomor:'',
            nominal:'',
            // harga:'',
            ep : '',
            name:'',
            code :'',
            logo:'',
            title:'',
            loading:true,
            list: 'CINNEMA 21',
            redirect:'',
            tipeProduct:'E Saldo'
        }
        this.handleChangeNomor= this.handleChangeNomor.bind(this);
        this.handleChange= this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e){
        this.setState({loading:true})
        e.preventDefault();
        const {nomor, code} = this.state
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        const header={
            'X-Api-Key' : X_API_KEY,
            // 'User-Agent' : USER_AGENT
        }
        const body = {
            'userID' :isLogin.userID,
            'accessToken' : isLogin.mbr_token,
            'aplUse': APLUS,
            'MSISDN':nomor,
            'productCode':code
        }
        // console.log(body)
        axios.post(API+ORDER_PRODUCT, body, {headers:header} )
            .then(res => {
                const order = res.data;
                this.setState({ order });
                // console.log(order)
                if(order.errNumber !== '0' ){
                    Swal.fire({
                        icon : 'error',
                        title: order.status,
                        text : order.respMessage
                    })
                    this.setState({loading: false})
                }else{
                    this.setState({loading: false})
                    Swal.fire({
                        icon : 'success',
                        title: order.status,
                        text : order.respMessage,
                        allowOutsideClick: false,
                    }).then((result) => {
                        if (result.value) {
                            const keterangan ={
                                'product' : this.state.tipeProduct,
                                'tipe' : this.state.tipe,
                                'ep': this.state.ep,
                                'logo':this.state.logo
                            }
                            const data = {order,keterangan}
                            this.props.changeReport(data)
                            this.setState({ redirect:true })
                        }
                    })
                }
                })
        .catch(error => {
        });
    }
    handleChange(e){
        this.setState({[e.target.name]:e.target.value})
        const nm = this.state.product.filter(
            (p) => {
                return p.code.indexOf(e.target.value) !== -1;
            }
        )
        // console.log(nm)
        this.setState({harga: nm[0].price})
        this.setState({ep: nm[0].ep})
        this.setState({name: nm[0].name})
        this.setState({code: nm[0].code})
    }
    handleChangeNomor(e){
        this.setState({[e.target.name]:e.target.value})
    }
    handleLayanan(data){
        const esaldo = this.state.product.filter(
            (p) => {
                return p.provider.indexOf(data) !== -1;
            }
        )
        
        if(this.state.list !== data){
            this.setState({ list : data })
        }
        this.setState({esaldo:esaldo})
        this.setState({title:data})
        if(data === 'CINNEMA 21' ){
            this.setState({logo:cinema})
        }else if(data === 'DANA'){
            this.setState({logo:dana})
        }else if(data === 'GO-JEK'){
            this.setState({logo:gopay})
        }else if(data === 'OVO CASH'){
            this.setState({logo:ovo})
        }else if(data === 'TCASH'){
            this.setState({logo:link})
        }else{
            this.setState({logo:tix})
        }
    }
    componentDidMount(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        if (isLogin){
            const header={
                'X-Api-Key' : X_API_KEY
            }
            const body ={
                'userID' : isLogin.userID,
                'aplUse':APLUS,
                'accessToken' : isLogin.mbr_token
            }
            axios.post(API+ESALDO_PRODUCT, body, {headers:header} )
            .then(res => {
                const product = res.data.data;
                this.setState({ product });
                this.setState({loading:false})
                this.handleLayanan('CINNEMA 21')
                // console.log('product',product)
                })
            .catch(error => {
            });
        }else{
            window.location = '/'
        }
        
    }
    render() {
        if(this.state.redirect === true){
            return <Redirect to='/home/detiletransaksi' />
        }
        if(this.state.loading === true){
            return(
                <LoadingPage/>
            )
        }
        return (
            <div>
                <div className='border shadow p-3 mt-3 mb-3'>
                    <div>
                        <div className="row ">
                            <div className='col-sm-12'>
                                <p style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} >Pilih Jenis Layanan</p>
                            </div>
                            <div className='col-sm-12 detile-product'>
                                <div onClick={() => this.handleLayanan('CINNEMA 21')} 
                                     className={this.state.list === 'CINNEMA 21' ?  'game-product ongame' : 'game-product'} >
                                     <div className='product-game-image row justify-content-center  align-items-center'>
                                        <img src={cinema} width='85px' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Saldo Cinema 21</p>
                                </div>
                                <div onClick={() => this.handleLayanan('DANA')} 
                                    className={this.state.list === 'DANA' ?  'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image row justify-content-center  align-items-center'>
                                        <img src={dana} width='85px' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Saldo Dana</p>
                                </div>
                                <div onClick={() => this.handleLayanan('GO-JEK')} 
                                    className={this.state.list === 'GO-JEK' ?  'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image row justify-content-center  align-items-center'>
                                        <img src={gopay} width='85px' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Saldo Gopay</p>
                                </div>
                                <div onClick={() => this.handleLayanan('OVO CASH')} 
                                    className={this.state.list === 'OVO CASH' ?  'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image row justify-content-center  align-items-center'>
                                        <img src={ovo} width='85px' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Saldo OVO</p>
                                </div>
                                <div onClick={() => this.handleLayanan('TCASH')} 
                                    className={this.state.list === 'TCASH' ?  'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image row justify-content-center  align-items-center'>
                                        <img src={link} width='85px' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Saldo Link-Aja</p>
                                </div>
                                <div onClick={() => this.handleLayanan('TIX-ID MOVIE')} 
                                    className={this.state.list === 'TIX-ID MOVIE' ?  'game-product ongame' : 'game-product'} >
                                    <div className='product-game-image row justify-content-center  align-items-center'>
                                        <img src={tix} width='85px' alt='logo' />
                                    </div>
                                    <p className='list-product mt-2' align='center'>Saldo Tix-ID</p>
                                </div>
                            </div>
                            
                            {/* Etoll */}
                            <div className="col-sm-12" id="Esaldo">
                                <hr className="my-1"></hr>
                                <div className="row">
                                    <div className='col'>
                                        <h5 style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} > {this.state.title} </h5>
                                    </div>
                                    <div className='mr-3'>
                                        {/* <img src={require('../../../images/EToll/E-Toll-BNI.svg')} width='100px' height='50px' alt='logo' /> */}
                                        <img src={this.state.logo} width='130px' height='50px' alt='logo' />
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className='col-sm mt-2' >
                                        <GreenTextField label="Nomor Pelanggan" style={{width:'100%'}} 
                                            name="nomor" 
                                            value={this.state.nomor} 
                                            onChange={this.handleChangeNomor}
                                            autoComplete='off' 
                                        />
                                    </div>
                                    <div className='col-sm mt-2' >
                                        <GreenFormControl style={{width:'100%'}}>
                                            <InputLabel htmlFor="nominal-native-simple">Nominal</InputLabel>
                                            <Select
                                                native
                                                value={this.state.nominal}
                                                onChange={this.handleChange}
                                                name="nominal"
                                            >
                                                <option value="" ></option>
                                                {this.state.esaldo.map(product=>(
                                                    <option className='mt-2' value={product.code} key={product.code}>{product.name}</option>
                                                ))}
                                            </Select>
                                        </GreenFormControl>
                                    </div>
                                    <div className='col-sm mt-2' >
                                        <GreenTextField label="Point" style={{width:'100%'}} 
                                            disabled value={this.state.ep}
                                        />
                                    </div>
                                </div>
                                <div className='row  mt-3'>
                                    <div className='col-sm-9'>
                                        <p style={{fontSize:'12px', color:'#707070',width:'100px'}} className='mb-0'>Harga</p>
                                        <span style={{fontSize:'36px', color:'#2E7D32',width:'200px'}} className='mb-0'><b>Rp. {convertCurrency(this.state.harga) }</b></span>
                                    </div>
                                    <div className='col-sm-3  align-self-end'>
                                        {this.state.ep === '' ? 
                                            <button style={style.btGreen} className='btn mt-4 rounded-0' disabled>Beli</button>
                                        :
                                            <button data-toggle="modal" data-target="#saldo" style={style.btGreen} className='btn mt-4 rounded-0'>Beli</button>
                                        }
                                    </div>
                                </div>
                            </div>
                            {/* End esaldo */}
                       
                        </div>
                    </div>
                </div>
                
                    {/* <!-- Modal --> */}
                    <div className="modal fade" id="saldo" data-backdrop="static" tabIndex="-1" role="dialog" aria-labelledby="saldoLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                            <div className="modal-header" style={{backgroundColor:'#005005',color:'white'}}>
                                <h5 className="modal-title" align='center' id="saldoLabel">INFORMASI PEMBELIAN</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span style={{color:'white'}} aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body pb-0">
                                <div className='row'>
                                    <div className='col mt-2'> {this.state.name} </div>
                                    <div className='mr-2'><img src={this.state.logo}  width='100px' height='50px' alt='logo'  /></div>
                                </div>
                                <div className='row mt-5'>
                                    <div className='col'>Nomor Pelanggan :</div>
                                    <div className='mr-3'> {this.state.nomor} </div>
                                </div>
                                <hr className="my-3"></hr>
                                <div className='row' style={{color:'rgb(28, 145, 245)'}}>
                                    <div className='col'>Bonus :</div>
                                    <div className='mr-3'> {this.state.ep} </div>
                                </div>
                                <hr className="mt-3 mb-0"></hr>
                                <div className='row mt-0 pt-2 pb-2' style={{backgroundColor:'rgb(48, 144, 54)'}}>
                                    <div className='col'>Jumlah Tagihan :</div>
                                    <div className='mr-3'> {convertCurrency(this.state.harga) } </div>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn  btn-outline-success" data-dismiss="modal">Batalkan</button>
                                <button type="button" onClick={this.handleSubmit}  data-dismiss="modal" className="btn btn-success">Ya, Lanjutkan</button>
                            </div>
                            </div>
                        </div>
                    </div>
            </div>
        )
    }
}


const mapDispatchToProps  = (dispatch) => ({
    changeReport : (data)=> dispatch({ type : 'REPORT_TRANSACTION', value : data  })
})

// export default Pulsa
export default connect(null, mapDispatchToProps)(Esaldo)



const style = {
    btGreen:{
        fontSize:'14px',
        color:'#FFFFFF',
        backgroundColor:'#2E7D32',
        width: '150px'
      }
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);
const GreenFormControl = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(FormControl);