import React, { Component } from 'react'
// import LoadingPage from '../loadingPage'
import '../../assets/styleRiwayat.css'
import { connect } from 'react-redux'
import DetileRiwayat from './menus/DetileRiwayat'

class Riwayat extends Component {
    constructor(){
        super()
        this.state={
            loading:true,
            history : [],
            list: 'Saldo',
            jenisRiwayat: 1,
            menuRiwayat: 'Riwayat Transaksi'
        }
    }
    setJenisRiwayat(data){
        if(data === 1) {
            this.setState({menuRiwayat: 'Riwayat Transaksi', jenisRiwayat:1})
        }else if(data === 2) {
            this.setState({menuRiwayat: 'Riwayat Deposit', jenisRiwayat:2})
        }else if(data === 3) {
            this.setState({menuRiwayat: 'Riwayat Saldo', jenisRiwayat:3})
        }else if(data === 4) {
            this.setState({menuRiwayat: 'Riwayat Penarikan', jenisRiwayat:4})
        }else  if(data === 5) {
            this.setState({menuRiwayat: 'Riwayat Bonus', jenisRiwayat:5})
        } else if(data === 6) {
            this.setState({menuRiwayat: 'Riwayat Transfer Antar Member', jenisRiwayat:data})
        }
    }

    lihatHistory(){
        // this.getDataTables(this.state.jenisRiwayat)
        this.props.dataRiwayat(this.state.jenisRiwayat)
    }

    componentDidMount() {
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        this.setState({isLogin:isLogin})
        if (!isLogin){
            window.location = '/'
        }
    }
    render() {
        return (
            <div>
                <div className='row mt-3'> 
                    <div className='container'> 
                        <div className="row m-0 mb-3" style={{boxShadow:'0px 0px 6px #00000029', backgroundColor:'#FFFFFF'}}>
                            <div className="col-sm-12 p-0">
                                <div style={{fontSize:'11px', color:'#FFFFFF', backgroundColor:'#2E7D32'}} className="p-1">
                                    <div className='row m-0'>
                                        <div className='' data-toggle="dropdown" aria-haspopup="true" aria-expanded="false"  >
                                            {this.state.jenisRiwayat === '' ?
                                                <p className='mb-0 ml-3 button-pilih p-2 pl-3 pr-3'>  
                                                    ........ 
                                                </p>
                                                    :
                                                <p className='mb-0 ml-3 button-pilih p-2 pl-3 pr-3'>  
                                                    {this.state.menuRiwayat}  <i style={{float:"right"}} className="fa mt-1 fa-chevron-down"> </i> 
                                                </p>
                                            }
                                        </div>
                                        <div style={{fontSize:'11px'}} className="dropdown-menu riwayat-list ml-3 rounded-0 p-0">
                                            <p onClick={()=>this.setJenisRiwayat(1)}> Riwayat Transaksi </p>
                                            <p onClick={()=>this.setJenisRiwayat(2)}> Riwayat Deposit </p>
                                            <p onClick={()=>this.setJenisRiwayat(3)}> Riwayat Saldo </p>
                                            <p onClick={()=>this.setJenisRiwayat(4)}> Riwayat Penarikan </p>
                                            <p onClick={()=>this.setJenisRiwayat(5)}> Riwayat Bonus </p>
                                            <p onClick={()=>this.setJenisRiwayat(6)}> Riwayat Transfer Antar Member </p>
                                        </div>
                                        <div className='col'>
                                            <button className="btn btn-warning p-2 pl-3 pr-3 buttonRiwayat rounded-0 text-light" onClick={()=>this.lihatHistory()}  style={{fontSize:'12px'}} type="submit">Lihat</button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className='col-sm-12 scroll-riwayat'>
                                <DetileRiwayat/>
                            </div>
                        </div>
                    </div> 
                </div>
            </div>
        )
    }
}
// const mapStateToProps = (state) =>({
//     data : state.firstReducer
// })

const mapDispatchToProps = dispatch =>({
    dataRiwayat : (data)=> dispatch({type:'DATA_RIWAYAT', value:{data}})
})

export default connect(null, mapDispatchToProps)(Riwayat)