import React, { Component } from 'react'
import {API,PASCABAYAR_PRODUCT,APLUS,X_API_KEY,PASCABAYAR_CEK} from '../../../api/index'
import axios from 'axios'
import {TextField,withStyles } from '@material-ui/core';
import LoadingPage from '../../loadingPage';
import { convertCurrency } from '../commons/commonLib'
import { connect } from 'react-redux'
import Swal from 'sweetalert2'
import { Redirect } from 'react-router-dom'

class finance extends Component {
    constructor(){
      super()
      this.state={
        cekTagihan :'',
        loading:true,
        product: [],
        nomor:'',
        nomorPelanggan:'',
        resTagihan:[],
        list:'',
        tipeProduct:'Finance',
        redirect:''
      }
      this.handleChange = this.handleChange.bind(this)
    }

    handleLayanan(data){
        this.setState({list:data})
        // console.log(data)
    }
    handleChange(e){
        this.setState({[e.target.name] : e.target.value})
    }
    cekTagihan(){
        this.setState({loading:true})
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        // const {product,} = this.state
        const header={
            'X-Api-Key' : X_API_KEY
        }
        const body = {
            'userID' :isLogin.userID,
            'accessToken' : isLogin.mbr_token,
            'aplUse': APLUS,
            'customerID' : this.state.nomorPelanggan,
            'customerMSISDN': this.state.nomor,
            'productCode': this.state.list.code
        }
        axios.post(API+PASCABAYAR_CEK, body, {headers:header})
            .then(res => {
            const order = res.data;
            if(order.errNumber !== '0'){
                Swal.fire({
                    icon : 'error',
                    title: order.status,
                    text : order.respMessage
                })
                this.setState({loading: false})
            }else{
                this.setState({
                resTagihan:order,
                loading: false, 
                cekTagihan:'open'
                })
            }
            // console.log(order)
            })
            .catch(error => {
            this.setState({loading: false})
                Swal.fire({
                allowOutsideClick: false,
                title: 'Request Tume Out',
                showClass: {
                    popup: 'animated fadeInDown faster'
                },
                hideClass: {
                    popup: 'animated fadeOutUp faster'
                }
                })
            });
    }
    componentDidMount(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        if (isLogin){
            const header={
                'X-Api-Key' : X_API_KEY
            }
            const body ={
                'userID' : isLogin.userID,
                'aplUse':APLUS,
                'accessToken' : isLogin.mbr_token,
                'productGroup' : 'FINANCE'
            }
            axios.post(API+PASCABAYAR_PRODUCT, body, {headers:header} )
            .then(res => {
                // console.log(res.data)
                if(res.data.errResponse === '8'){
                    this.setState({ product:res.data, loading:false });
                }else{
                    const product = res.data.productList.filter(
                        (p) => {
                            return p.isactive.indexOf('Active') !== -1;
                        }
                    )
                    this.setState({ product:product,list:product[0], loading:false });
                }
            })
            .catch(error => {
            });
        }else{
            window.location = '/'
        }
        if(!this.props.profil.data[0].o_hp){

        }else{
            this.setState({nomor:this.props.profil.data[0].o_hp})
        }
      }
    render() {
        if(this.state.redirect === true){
            return <Redirect to='/home/detiletagihan' />
        }
        const {resTagihan,product} = this.state
        // console.log(this.props.profil)
        
        if(this.state.loading === true){
            return (
                <LoadingPage/>
            )
        }
        return (
            <div>
                <div className='border shadow p-3 mt-3 mb-3'>
                    <div>
                        <div className="row ">
                            <div className='col-sm-12'>
                                <p style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} >Finance</p>
                            </div>
                            <div id='game' className='col-sm-12 detile-product'>
                                {product.map(prod => 
                                    <div onClick={() => this.handleLayanan(prod)}  key={prod.code}
                                        className={this.state.list.code === prod.code ? 'game-product ongame' : 'game-product'} >
                                        <div className='product-game-image row justify-content-center  align-items-center'>
                                            <img src={prod.image} width='60px' alt='logo' />
                                        </div>
                                        <p className='list-product mt-2' align='center'>{prod.name}</p>
                                    </div>
                                )}
                            </div>
                        </div>

                        {/* Etoll */}
                        <div className="col-sm-12" id="ETollBNI">
                                <hr className="my-1"></hr>
                                <div className="row">
                                    <div className='col'>
                                        <h5 style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} > {this.state.list.name} </h5>
                                    </div>
                                    <div className='mr-3'>
                                        <img src={this.state.list.image} width='100px' height='50px' alt='logo' />
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className='col-sm mt-2' >
                                        <GreenTextField label="Nomor Pelanggan" style={{width:'100%'}} 
                                            name="nomorPelanggan" 
                                            value={this.state.nomorPelanggan} 
                                            onChange={this.handleChange}
                                            autoComplete='off'
                                        />
                                    </div>
                                    <div className='col-sm mt-2' >
                                        <GreenTextField label="Nomor Hp" style={{width:'100%'}} 
                                            name="nomor" 
                                            value={this.state.nomor} 
                                            onChange={this.handleChange}
                                        />
                                        <span style={style.validate}>Nomor digunakan untuk menerima konfirmasi</span>
                                    </div>
                                    <div className='col-sm mt-2' >
                                        {this.state.nomor === '' ? 
                                            <button style={style.btGreen} className='btn mt-4 rounded-0' disabled>Cek Tagihan</button>
                                        :
                                            <button onClick={()=>this.cekTagihan()} style={style.btGreen} className='btn mt-4 rounded-0'>Cek Tagihan</button>
                                        }
                                    </div>
                                </div>
                                <div className='row  mt-3'>
                                    <div className='col-sm-9'>
                                        <p style={{fontSize:'12px', color:'#707070',width:'100px'}} className='mb-0'>Harga</p>
                                        <span style={{fontSize:'36px', color:'#2E7D32',width:'200px'}} className='mb-0'><b>Rp. {convertCurrency(this.state.harga) }</b></span>
                                    </div>
                                </div>
                            </div>
                            {/* End Etoll */}

                    </div>
                </div>

                {!this.state.cekTagihan ? <div></div> : 
                <div className='border shadow p-3 mt-3 mb-3'>
                    <div className="row mt-2">
                        <div className='col-sm-12'>
                            <h5 style={{color:'#2E7D32'}} >DETAIL TRANSAKSI</h5>
                        </div>
                    </div>
                    <hr className='my-1'></hr>
                    <div className='row mt-2'>
                      <table className='table table-borderless'>
                        <tbody>
                          <tr>
                            <td>Transaction ID</td>
                            <td>:</td>
                            <td>{resTagihan.billingReferenceID}</td>
                          </tr>
                          <tr>
                            <td>ID Customer</td>
                            <td>:</td>
                            <td>{resTagihan.customerID}</td>
                          </tr>
                          <tr>
                            <td>Name</td>
                            <td>:</td>
                            <td>{resTagihan.customerName}</td>
                          </tr>
                          <tr>
                            <td>Phone</td>
                            <td>:</td>
                            <td>{resTagihan.customerMSISDN}</td>
                          </tr>
                          <tr>
                            <td>Date</td>
                            <td>:</td>
                            <td>{resTagihan.respTime}</td>
                          </tr>
                          <tr>
                            <td>Payment</td>
                            <td>:</td>
                            <td>Rp.  {convertCurrency(resTagihan.payment)}</td>
                          </tr>
                          <tr>
                            <td>Admin Bank</td>
                            <td>:</td>
                            <td>Rp. {convertCurrency(resTagihan.adminBank)}</td>
                          </tr>
                          <tr>
                            <td>Billink</td>
                            <td>:</td>
                            <td> Rp. {convertCurrency(resTagihan.billing)}</td>
                          </tr>
                          <tr>
                            <td>Periode</td>
                            <td>:</td>
                            <td>{resTagihan.period}</td>
                          </tr>
                          <tr>
                            <td>Notice</td>
                            <td>:</td>
                            <td>{resTagihan.status}</td>
                          </tr>
                          <tr>
                            <td>Point</td>
                            <td>:</td>
                            <td>{resTagihan.ep}</td>
                          </tr>
                        </tbody>  
                      </table>
                    </div>
                    <div className='row mt-3 ml-0'>
                        <button style={style.btGreen}  data-toggle="modal" data-target="#finance"  className='btn rounded-0'>PROSES</button>
                    </div>
                </div>
                } 

                  {/* <!-- Modal --> */}
                  <div className="modal fade" id="finance" data-backdrop="static" tabIndex="-1" role="dialog" aria-labelledby="financeLabel" aria-hidden="true">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                        <div className="modal-header" style={{backgroundColor:'#005005',color:'white'}}>
                            <h5 className="modal-title" align='center' id="financeLabel">INFORMASI PEMBELIAN</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span style={{color:'white'}} aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body pb-0">
                            <div className='row'>
                                <div className='col'>Tanggal :</div>
                                <div className='mr-3'>{resTagihan.respTime}</div>
                                
                            </div>
                            <div className='row mt-4'>
                                <div className='col'>Jumlah Tagihan :</div>
                                <div className='mr-3'> Rp.  {convertCurrency(resTagihan.payment)} </div>
                            </div>
                            <div className='row mt-2'>
                                <div className='col'>Biaya Transaksi :</div>
                                <div className='mr-3'> Rp.  {convertCurrency(resTagihan.adminBank)} </div>
                            </div>
                            <hr className="mt-3 mb-0"></hr>
                            <div className='row mt-0 pt-2 pb-2' style={{backgroundColor:'rgb(48, 144, 54)'}}>
                                <div className='col'>Jumlah Tagihan :</div>
                                <div className='mr-3'>Rp. {convertCurrency(resTagihan.billing) } </div>
                            </div>
                            <div className='row mt-2'>
                                <div className='col' style={{color:'#2E7D32'}}>No Pelanggan :</div>
                                <div className='mr-3' style={{color:'#2E7D32'}}> {resTagihan.customerID} </div>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn  btn-outline-success" data-dismiss="modal">Batalkan</button>
                            <button type="button" onClick={this.handleOrder}  data-dismiss="modal" className="btn btn-success">Ya, Lanjutkan</button>
                        </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}
const mapStateToProps = (state) => ({
    profil : state.riwayatTransaksi.profile
})
const mapDispatchToProps  = (dispatch) => ({
  changeReport : (data)=> dispatch({ type : 'REPORT_TRANSACTION', value : data  })
})
export default connect(mapStateToProps, mapDispatchToProps)(finance)
const style = {
    btGreen:{
        fontSize:'14px',
        color:'#FFFFFF',
        backgroundColor:'#2E7D32',
        width: '150px'
      },
      validate:{
          fontSize:'8pt',
          color:'#DB0C0B'
      }
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);