import React, { Component } from 'react'
import axios from 'axios'
import {API, GET_TOTAL_DOWNLINE, X_API_KEY} from '../../api/index'
import LoadingPage from '../loadingPage'

class DaftarMember extends Component {
    constructor(){
        super()
        this.state={
            loading:true,
            downline:[]
        }
    }


    componentDidMount() {
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        this.setState({isLogin:isLogin})
        // console.log(isLogin)
        if (!isLogin){
            window.location = '/'
        } else {
            const header={
                'X-Api-Key' : X_API_KEY
            }
            const body ={
                'userID' : isLogin.userID,
                // 'aplUse':APLUS,
                // 'accessToken' : isLogin.mbr_token,
                // 'requestID' : 1,
                // 'start_date' : '2019-10-12 00:00:00'
            }
            
            axios.post(API+GET_TOTAL_DOWNLINE, body, {headers:header} )
            .then(res => {
                const downline = res.data;
                // console.log(downline)
                this.setState({ downline :downline, loading:false });
            })
            .catch(error => {
                console.log(error.response)
            });
        }
    }


    render() {
        if(this.state.loading === true){
            return(
                <LoadingPage/>
            ) 
        }
        return (
            <div>
                <div className='row mt-3'> 
                    <div className='container'> 
                        <div className="row m-0 mb-3" style={{height:'500px'}}>
                            <div className="col p-0" style={{boxShadow:'0px 0px 6px #00000029', backgroundColor:'#FFFFFF', height:'500px'}}>
                                <div style={{fontSize:'11px', color:'#FFFFFF', backgroundColor:'#2E7D32'}} className="p-3">
                                <div className="form-inline my-2 my-lg-0">
                                    <input className="form-control  rounded-0"  style={{fontSize:'12px'}} type="search" placeholder="Search" aria-label="Search"/>
                                    <button className="btn btn-warning my-2 my-sm-0 rounded-0 text-light"  style={{fontSize:'12px'}} type="submit">Search</button>
                                </div>
                                </div>
                                <div className='p-3'>
                                    
                                </div>
                            </div>
                        </div>
                    </div> 
                </div>
            </div>
        )
    }
}
export default DaftarMember