import React, { useState, useEffect } from 'react';
import DataTables from './DataTable';
import Pagination from '../Paginate';
import {API, GET_HISTORY, X_API_KEY, APLUS} from '../../../../api/index'
import axios from 'axios';
import '../../../../assets/radioButton.css'

const DepositPage = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(10);

  useEffect(() => {
    const fetchPosts = async () => {
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        const header={
            'X-Api-Key' : X_API_KEY
        }
        const body ={
            'userID' : isLogin.userID,
            'aplUse':APLUS,
            'accessToken' : isLogin.mbr_token,
            'requestID' : 2,
        }
      setLoading(true);
      const res = await axios.post(API+GET_HISTORY, body, {headers:header} )
      // console.log(res.data.listData)
      if(res.data.errNumber !== '0'){
        setPosts('Riwayat Tidak Ada');
        setLoading(false);
      }else{
        setPosts(res.data.listData);
        setLoading(false);
      }
    };

    fetchPosts();
  }, []);

  // Get current posts
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = posts.slice(indexOfFirstPost, indexOfLastPost);

  // Change page
  const paginate = pageNumber => setCurrentPage(pageNumber);

  return (
      <div>
            <table id='Table' className="table table-bordered">
                <thead>
                    <tr className='table-active'>
                        <td>Tanggal</td>
                        <td>Nilai</td>
                        <td>Tujuan</td>
                        {/* <td>Rekening</td>
                        <td>Pemilik</td> */}
                        <td>Status</td>
                        {/* <td>Action</td> */}
                    </tr>
                </thead>
                    <DataTables posts={currentPosts} loading={loading} />
                <thead>
                    <tr className='table-active'>
                        <td>Tanggal</td>
                        <td>Nilai</td>
                        <td>Tujuan</td>
                        {/* <td>Rekening</td>
                        <td>Pemilik</td> */}
                        <td>Status</td>
                        {/* <td>Action</td> */}
                    </tr>
                </thead>
            </table>
        <Pagination
            postsPerPage={postsPerPage}
            totalPosts={posts.length}
            paginate={paginate}
        />
      </div>
  );
};

export default DepositPage;