import React from 'react';
import { convertCurrency } from '../../../payment/commons/commonLib'

const DataTables = ({ posts, loading }) => {


  if (loading) {
    return (
        <tbody>
            <tr> 
                <td colSpan='5' align='center'> 
                    Loading...
                </td>
            </tr>
        </tbody>);
  }


// console.log(posts)
  return (
    <tbody>
      {!posts[0] || posts === 'Riwayat Ti' ? 
        <tr> 
            <td colSpan='5' align='center'> 
                Data Tidak Ada
            </td>
        </tr>
      : 
        posts.map(posts => (
            <tr key={posts.transaksi_id}>
                <td>{posts.transaksi_id}</td> 
                <td>{posts.tgl}</td>
                <td> <b>Product :</b> {posts.product_name} <br/> <b>Tujuan :</b> { posts.tujuan } <br/> <b>Nominal :</b> Rp. {convertCurrency(posts.harga)} </td>
                <td>{posts.vstatus}</td>
            </tr>
        ))
      }
    </tbody>
  );
};


export default DataTables;