import React, { Component } from 'react'
import axis from '../../../images/Profider/axis.png'
import indosat from '../../../images/Profider/indosat.png'
import smartfren from '../../../images/Profider/smartfren.png'
import telkomsel from '../../../images/Profider/telkomsel.png'
import three from '../../../images/Profider/three.png'
import xl from '../../../images/Profider/xlx.png'
import { Redirect } from 'react-router-dom'
import {API,PULSA_PRODUCT,APLUS,X_API_KEY,PULSA_PREFIX,ORDER_PRODUCT} from '../../../api/index'
import axios from 'axios'
import {TextField,InputLabel,Select,FormControl,withStyles } from '@material-ui/core';
import {convertCurrency} from '../commons/commonLib'
import LoadingPage from '../../loadingPage'
import Swal from 'sweetalert2'
import { connect } from 'react-redux'

class Pulsa extends Component {
    constructor(){
        super()
        this.state={
            prefix:[],
            product:[],
            nomor:'',
            tipe:'Kosong',
            nominal:'',
            ep : '',
            name:'',
            code :'',
            logo : '',
            loading: false,
            redirect:'',
            productFilter:'',
            tipeProduct:'Pulsa'

        }
        this.handleChange= this.handleChange.bind(this);
        this.handleChangeNomor= this.handleChangeNomor.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    
    handleChangeNomor(e){
        this.setState({[e.target.name]:e.target.value})
        const nomor = e.target.value.substr(0,4)
        const pref = this.state.prefix.filter(
            (pref) => {
                return pref.prefix.indexOf(nomor) !== -1;
            }
        )
        if(!pref[0] || nomor === ''){
            this.setState({tipe: 'Kosong', productFilter:''})
        }else{
            const pulsa = this.state.product.filter(
                (p) => {
                    return p.name.indexOf(pref[0].provider.toUpperCase()) !== -1;
                }
            )
            this.setState({productFilter: pulsa, tipe:pref[0].provider})
        }
        
        // console.log(pref[0].provider)
        if (!pref[0]){
            this.setState({logo : ''})
        }else if (pref[0].provider === 'Indosat'){
            this.setState({logo : indosat})
        }else if(pref[0].provider === 'Three'){
            this.setState({logo: three})
        }else if(pref[0].provider === 'Smart'){
            this.setState({logo: smartfren})
        }else if(pref[0].provider === 'Axis'){
            this.setState({logo: axis})
        }else if(pref[0].provider === 'Telkomsel'){
            this.setState({logo: telkomsel})
        }else if(pref[0].provider === 'XL'){
            this.setState({logo: xl})
        }else{
            this.setState({logo: ''})
        }
    }
    handleChange(e){
        this.setState({[e.target.name]:e.target.value})
        const nm = this.state.product.filter(
            (p) => {
                return p.code.indexOf(e.target.value) !== -1;
            }
        )
        this.setState({harga: nm[0].price, ep: nm[0].ep, name: nm[0].name,code: nm[0].code  })
    }
    handleSubmit(e){
        e.preventDefault();
        this.setState({loading: true})
        const {nomor, code} = this.state
        const isLogin = JSON.parse(localStorage.getItem('isLogin'))
        const header={
            'X-Api-Key' : X_API_KEY
        }
        const body = {
            'userID' :isLogin.userID,
            'accessToken' : isLogin.mbr_token,
            'aplUse': APLUS,
            'MSISDN':nomor,
            'productCode':code
        }
        axios.post(API+ORDER_PRODUCT, body, {headers:header} )
            .then(res => {
                const order = res.data;
                // console.log(order)
                if(order.errNumber !== '0' ){
                    Swal.fire({
                        icon : 'error',
                        title: order.status,
                        text : order.respMessage
                    })
                    this.setState({loading: false})
                }else{
                    this.setState({loading: false})
                    // Swal.fire({
                    //     icon : 'success',
                    //     title: order.status,
                    //     text : order.respMessage
                    // })
                    Swal.fire({
                        icon : 'success',
                        title: order.status,
                        text : order.respMessage,
                        allowOutsideClick: false,
                    }).then((result) => {
                        if (result.value) {
                            const keterangan ={
                                'product' : this.state.tipeProduct,
                                'tipe' : this.state.tipe,
                                'ep': this.state.ep,
                                'logo':this.state.logo
                            }
                            const data = {order,keterangan}
                            this.props.changeReport(data)
                            this.setState({ redirect:true })
                        }
                    })
                }

            })
            .catch(error => {
                this.setState({loading: false})
                  Swal.fire({
                    allowOutsideClick: false,
                    title: 'Request Tume Out',
                    showClass: {
                      popup: 'animated fadeInDown faster'
                    },
                    hideClass: {
                      popup: 'animated fadeOutUp faster'
                    }
                  })
            });
    }
    componentDidMount(){
        const isLogin = JSON.parse(localStorage.getItem('isLogin')) 
        // console.log(isLogin)
        if (isLogin){
            const header={
                'X-Api-Key' : X_API_KEY
            }

            const body ={
                'userID' : isLogin.userID,
                'aplUse':APLUS,
                'accessToken' : isLogin.mbr_token
            }

            
            axios.post(API+PULSA_PRODUCT, body, {headers:header} )
            .then(res => {
                const product = res.data.data;
                this.setState({ product : product, loading: false });
            })
            .catch(error => {
                console.log(error.response)
            });


            axios.post(API+PULSA_PREFIX, body, {headers:header} )
            .then(res => {
                const prefix = res.data.data;
                this.setState({ prefix });
            })
            .catch(error => {
            });
        }else{
            window.location = '/'
        }
        
    }

    render() {
        if(this.state.redirect === true){
            return <Redirect to='/home/detiletransaksi' />
        }
        if(this.state.loading === true){
            return (
                <LoadingPage/>
            )
        }
        let pulsa = this.state.productFilter
        return (
            <div>
                <div className=' border shadow p-3 mt-3 mb-3'>
                    <div className="row mt-2">
                        <div className='col-sm-12'>
                            <h3 style={{color:'#2E7D32',marginTop:'10px',marginBottom:'10px'}} >Pembelian Pulsa</h3>
                        </div>
                    </div>
                    <div className='row'>
                        <div className='col-sm mt-2' >
                            <GreenTextField label="Nomor Telepon" style={{width:'100%'}} 
                                name="nomor" 
                                value={this.state.nomor} 
                                onChange={this.handleChangeNomor}
                                autoComplete='off' 
                            />
                        </div>
                        <div className='col-sm mt-2' >
                            <GreenFormControl style={{width:'100%'}}>
                                <InputLabel htmlFor="nominal-native-simple">Nominal</InputLabel>
                                { pulsa === '' ? 
                                    <Select  native >
                                        <option value="" ></option>
                                    </Select> 
                                :
                                <Select
                                    native
                                    value={this.state.nominal}
                                    onChange={this.handleChange}
                                    name="nominal"
                                >
                                    <option value="" ></option>
                                    {pulsa.map(pulsa=>(
                                        <option className='mt-2' value={pulsa.code} key={pulsa.code}>{pulsa.name}</option>
                                    ))}
                                </Select>
                                }
                            </GreenFormControl>
                        </div>
                        <div className='col-sm mt-2' >
                            <GreenTextField label="Point" style={{width:'100%'}} 
                                disabled value={this.state.ep}
                            />
                        </div>
                    </div>
                    <div className='row  mt-3'>
                        <div className='col-sm-9'>
                            <p style={{fontSize:'12px', color:'#707070',width:'100px'}} className='mb-0'>Harga</p>
                            <span style={{fontSize:'36px', color:'#2E7D32',width:'200px'}} className='mb-0'><b>Rp. {convertCurrency(this.state.harga) }</b></span>
                        </div>
                        <div className='col-sm-3  align-self-end'>
                            {this.state.ep === '' ? 
                                <button style={style.btGreen} className='btn mt-4 rounded-0' disabled>Beli</button>
                            :
                                <button data-toggle="modal" data-target="#staticBackdrop" style={style.btGreen} className='btn mt-4 rounded-0'>Beli</button>
                            }
                        </div>
                    </div>

                    {/* <!-- Modal --> */}
                    <div className="modal fade" id="staticBackdrop" data-backdrop="static" tabIndex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                        <div className="modal-dialog" role="document">
                            <div className="modal-content">
                            <div className="modal-header" style={{backgroundColor:'#005005',color:'white'}}>
                                <h5 className="modal-title" align='center' id="staticBackdropLabel">INFORMASI PEMBELIAN</h5>
                                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span style={{color:'white'}} aria-hidden="true">&times;</span>
                                </button>
                            </div>
                            <div className="modal-body pb-0">
                                <div className='row'>
                                    <div className='col-sm-9  align-self-center'>{this.state.name}</div>
                                    <div className='col-sm-3'>
                                        <img src={this.state.logo} alt={this.state.logo} width='100%' height='100%' className='mr-3' />
                                    </div>
                                </div>
                                <div className='row mt-5'>
                                    <div className='col'>Nomor Pelanggan :</div>
                                    <div className='mr-3'> {this.state.nomor} </div>
                                </div>
                                <hr className="my-3"></hr>
                                <div className='row' style={{color:'rgb(28, 145, 245)'}}>
                                    <div className='col'>Bonus :</div>
                                    <div className='mr-3'> {this.state.ep} </div>
                                </div>
                                <hr className="mt-3 mb-0"></hr>
                                <div className='row mt-0 pt-2 pb-2' style={{backgroundColor:'rgb(48, 144, 54)'}}>
                                    <div className='col'>Jumlah Tagihan :</div>
                                    <div className='mr-3'> {convertCurrency(this.state.harga) } </div>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn  btn-outline-success" data-dismiss="modal">Batalkan</button>
                                <button type="button" onClick={this.handleSubmit}  data-dismiss="modal" className="btn btn-success">Ya, Lanjutkan</button>
                            </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )

    }
}


const mapDispatchToProps  = (dispatch) => ({
    changeReport : (data)=> dispatch({ type : 'REPORT_TRANSACTION', value : data  })
})

// export default Pulsa
export default connect(null, mapDispatchToProps)(Pulsa)
const style = {
    btGreen:{
        fontSize:'14px',
        color:'#FFFFFF',
        backgroundColor:'#2E7D32',
        width: '150px'
      }
}
const GreenTextField = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(TextField);
const GreenFormControl = withStyles({
    root: {
      '& label.Mui-focused': {
        color: 'green',
      },
      '& .MuiInput-underline:after': {
        borderBottomColor: 'green',
      },
      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: 'red',
        },
        '&:hover fieldset': {
          borderColor: 'yellow',
        },
        '&.Mui-focused fieldset': {
          borderColor: 'green',
        },
      },
    },
  })(FormControl);